import React from 'react';

import { useRouteMatch } from 'react-router-dom';

/**
 * components
 */
import ProfileCard from './components/ProfileCard';

/**
 * styles
 */
import { Container, Brand, BrandView, CustomLink } from './styles';

/**
 * Rotas
 */
import { channelManagerRoutes } from '../../../../routes/channelManager.routes';

export default function SidebarComponent() {
  return (
    <Container>
      <BrandView>
        <Brand />
      </BrandView>

      <ProfileCard />

      {channelManagerRoutes.map(({ sidebar, ...route }, key) => {
        if (sidebar) {
          return (
            <Link
              path={`${route.layout}${route.path}`}
              title={route.sideBarTitle}
              key={key}
            />
          );
        }
      })}
    </Container>
  );
}

function Link({ path, title, key }) {
  const focused = useRouteMatch({
    path: path,
    exact: true,
  });
  return (
    <CustomLink {...{ focused }} key={key} to={path}>
      <p>{title}</p>
    </CustomLink>
  );
}
