import styled, { css } from 'styled-components';

export const InviteSectionItem = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 16px;
`;

export const InviteSectionItemInput = styled.div`
  width: 254px;
  margin-right: 24px;
`;

const InviteButtonSendedStyled = css`
  color: #4be300;
  border: 1px solid #4be300;
  background-color: #fff;

  &:hover {
    background-color: #fff;
  }
`;

export const InviteButton = styled.button`
  width: 150px;
  border: 1px solid #ebc1f5;
  outline: none;
  padding: 7px 24px;
  font-family: 'Poppins';
  font-size: 12px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  color: #72008d;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  background-color: #fcf0ff;
  cursor: pointer;
  margin-bottom: -16px;

  &:disabled {
    background: #f4f4f4;
    color: #cecece;
    border: 0;
    cursor: not-allowed;
  }

  &:disabled:hover {
    background: #f4f4f4;
    color: #cecece;
  }

  &:hover {
    background-color: #e7ceed;
  }

  ${({ invited }) => invited && InviteButtonSendedStyled}
`;
