import React from 'react';

import { Link as RouterLink } from 'react-router-dom';

import { Container, UserName, Link } from './styles';

import { useAuth } from '../../../../contexts/auth.context';

function User() {
  const { user, signOut } = useAuth();

  return (
    <Container>
      <RouterLink to="/gc">
        <UserName>
          <strong>Olá, {user.first_name} ||</strong> Acessar GC
        </UserName>
      </RouterLink>

      <Link onClick={signOut}>Sair da conta</Link>
    </Container>
  );
}

export default User;
