import React from 'react';

import { Link } from 'react-router-dom';

import { Container, ChannelScroll, ClaimLink } from './styles';

import ChannelItem from './components/ChannelItem';

import { useAuth } from '../../../../../../../../contexts/auth.context';

function ChannelList() {
  const {
    user: { channels },
  } = useAuth();

  return (
    <Container>
      <ChannelScroll>
        {channels &&
          channels.map((channel, key) => (
            <ChannelItem channelId={channel} key={key} index={key} />
          ))}
      </ChannelScroll>

      <Link to="/reivindicacao">
        <ClaimLink>+ Reivindicar novo canal</ClaimLink>
      </Link>
    </Container>
  );
}

export default React.memo(ChannelList);
