import styled, { css } from 'styled-components';

export const Container = styled.div`
  max-width: 654px;
`;

export const Description = styled.p`
  font-family: 'Poppins';
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.71;
  letter-spacing: 0.28px;
  text-align: left;
  color: #000000;
`;

export const CheckBoxContainer = styled.div`
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-top: 34px;
  margin-bottom: 42px;
`;

const CheckBoxTextCoreCss = css`
  font-family: 'Poppins';
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.83;
  letter-spacing: 0.24px;
  text-align: left;
  margin: 0;
`;

export const CheckboxDefaultText = styled.p`
  ${CheckBoxTextCoreCss}
`;

export const CheckboxStrongText = styled.span`
  font-weight: 600;
  letter-spacing: normal;
  color: #72008d;
  text-decoration-line: underline;
  cursor: pointer;
`;
