import styled from 'styled-components';

export const TextAreaLengthText = styled.p`
  margin: 0;
  font-family: 'Poppins';
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: 0.2px;
  text-align: right;
  color: #000000;
  padding-top: 8px;
`;

export const IconContainer = styled.div`
  margin: 0px;
  padding: 0px;
  align-self: flex-end;
  color: ${({ error }) => (error ? '#FF2727' : '#BC79CC')};
  margin-bottom: -2px;

  svg {
    font-size: 22px !important;
  }
`;
