import React, { useState } from 'react';

import { Lock } from '@material-ui/icons';

import PropTypes from 'prop-types';

import { format } from 'date-fns';

import {
  Container,
  AdInformationContainer,
  AdHandlersContainer,
  AdInformationItem,
  AdInformationLabel,
  AdInformationLabelContainer,
  AdInformationData,
  AdHandlerLink,
} from './styles';

/**
 * assets
 */
import DefaultAdImage from '../../../../../../../assets/png/Android_icon_CH.png';

import CustomModal from '../../../../../../../components/CustomModal';
import CustomInput from '../../../../../../../components/CustomInput';
import { CustomButton } from '../../../../../../../components/GerenciadorCanais';

import AdPreview from '../AdItemPreview';

import { useUnform } from '../../../../../../../modules/unform.module';

function AdItem(props) {
  const [state, setState] = useState(props);
  const [deleteModalOpen, toggleDeleteModalOpen] = useState(false);
  const [changelStatusModalOpen, toggleChangelStatusModalOpen] = useState(
    false
  );

  const { status, createdAt, clicks } = state;
  const { Form, UnformInput, UnformTextArea } = useUnform();

  return (
    <Container>
      <AdPreview {...state} />

      <AdInformationContainer>
        <AdInformationItem>
          <AdInformationLabelContainer>
            <AdInformationLabel>Status:</AdInformationLabel>
            <AdHandlerLink onClick={handleChangeStatusModalOpen}>
              Alterar
            </AdHandlerLink>
          </AdInformationLabelContainer>
          <AdInformationData>{status}</AdInformationData>
        </AdInformationItem>
        <AdInformationItem>
          <AdInformationLabelContainer>
            <AdInformationLabel>Criado em: </AdInformationLabel>
          </AdInformationLabelContainer>
          <AdInformationData>
            {format(new Date(createdAt), 'dd/MM/yyyy')} as{' '}
            {format(new Date(createdAt), 'HH:mm')}
          </AdInformationData>
        </AdInformationItem>
        <AdInformationItem>
          <AdInformationLabelContainer>
            <AdInformationLabel>Cliques:</AdInformationLabel>
          </AdInformationLabelContainer>
          <AdInformationData>{clicks}</AdInformationData>
        </AdInformationItem>
      </AdInformationContainer>
      <AdHandlersContainer>
        <AdHandlerLink onClick={handleDeleteModalOpen}>Apagar</AdHandlerLink>
      </AdHandlersContainer>

      {DeleteAdModal()}

      {ChangeAdStatusModal()}
    </Container>
  );

  function DeleteAdModal() {
    const [customButtonDisabled, toggleCustomButtonDisabled] = useState(true);
    return (
      <CustomModal
        title={'Apagar anúncio'}
        description={
          'Tem certeza que deseja apagar o anúncio? Esta ação apagar todos os dados, inclusive os cliques, e não poderá ser desfeita. Se deseja continuar coloque sua senha.'
        }
        toggleModalOpen={handleDeleteModalOpen}
        isOpen={deleteModalOpen}
      >
        <Form onSubmit={(data) => {}}>
          <UnformInput
            style={{ color: '#bc79cc' }}
            type="password"
            label="Senha"
            name="delete-ad-password"
            id="delete-ad-password"
            placeholder="Senha"
            iconStart={<Lock />}
            contentStyle={{ marginBottom: 32, marginTop: -16 }}
            onChangeCallback={handlePasswordInput}
          />

          <CustomButton
            onClick={handleDeleteChannelAd}
            disabled={customButtonDisabled}
          >
            Apagar
          </CustomButton>
        </Form>
      </CustomModal>
    );
    function handlePasswordInput(inputText) {
      toggleCustomButtonDisabled(inputText.length === 0);
    }

    function handleDeleteChannelAd() {}
  }

  function handleDeleteModalOpen() {
    toggleDeleteModalOpen((prev) => !prev);
  }

  function ChangeAdStatusModal() {
    return (
      <CustomModal
        title={status === 'Ativado' ? 'Desativar anúncio' : 'Ativar anúncio'}
        description={
          status === 'Desativado'
            ? 'Somente 1 anúncio pode permanecer ativo, ao ativar este anúncio o atual será desativado. Tem certeza que deseja prosseguir?'
            : 'Se prosseguir, seu anúncio será desativado, podendo reativá-lo depois. Tem certeza que deseja prosseguir?'
        }
        closeModalText={status === 'Ativado' ? 'Não, cancelar' : 'Cancelar'}
        toggleModalOpen={handleChangeStatusModalOpen}
        isOpen={changelStatusModalOpen}
      >
        <CustomButton onClick={handleChannelAdStatus}>Sim</CustomButton>
      </CustomModal>
    );

    function handleChannelAdStatus() {
      setState({
        ...state,
        status: status === 'Ativado' ? 'Desativado' : 'Ativado',
      });

      handleChangeStatusModalOpen();
    }
  }

  function handleChangeStatusModalOpen() {
    toggleChangelStatusModalOpen((prev) => !prev);
  }
}

export default React.memo(AdItem);

AdItem.propTypes = {
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
  cta: PropTypes.string.isRequired,
  website: PropTypes.string.isRequired,
  status: PropTypes.string.isRequired,
  createdAt: PropTypes.string.isRequired,
  clicks: PropTypes.number.isRequired,
};

AdItem.defaultProps = {
  title: 'Título do anúcio',
  description: 'Descrição do anúncio',
  image: DefaultAdImage,
  cta: 'Acesse o site',
  website: '',
  status: 'Ativado',
  createdAt: 'Mon May 25 2020 20:39:01 GMT-0300',
  clicks: 0,
};
