import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';

/**
 * components
 */

import Navbar from './components/Navbar';
import Card from './components/Card';

/**
 * styled components
 */
import { Container, Content } from './styles';

/**
 * Rotas
 */
import { channelManagerRoutes } from '../../routes/channelManager.routes';

export default function Reivindicacao() {
  return (
    <Container>
      <Navbar />
      <Content>
        <Switch>
          {channelManagerRoutes.map(({ Component, ...route }, key) => (
            <Route
              key={key}
              exact
              path={`${route.layout}${route.path}`}
              render={(props) => (
                <Card {...route}>
                  <Component {...props} />
                </Card>
              )}
            />
          ))}
          <Redirect from="/gc" to="/gc/home" />
        </Switch>
      </Content>
    </Container>
  );
}
