import styled from 'styled-components';

export const Badge = styled.div`
  align-items: center;
  padding-left: 16px;
  display: flex;
  flex-direction: row;
  background: #62ffb1 0% 0% no-repeat padding-box;
  width: 137px;
  height: 45px;
  border-radius: 50px;
  margin-bottom: 24px;
`;

export const BadgeText = styled.p`
  text-align: center;
  font-family: 'Poppins';
  font-weight: 700;
  font-size: 14px;
  letter-spacing: 0;
  color: #72008d;
`;

export const InputLinksSection = styled.p`
  font-family: 'Poppins';
  font-size: 14px;
  text-align: center;
  font-weight: 600;
  letter-spacing: 0px;
  color: #72008d;
  opacity: 1;
`;

export const InputLinksSectionUnderline = styled.p`
  font-family: 'Poppins';
  font-size: 14px;
  text-align: center;
  font-weight: 500;
  letter-spacing: 0px;
  text-decoration-line: underline;
  color: #72008d;
  opacity: 1;
`;
