import React from 'react';
import { Link } from 'react-router-dom';

import maskEmail from '../../../utils/maskEmail';

import {
  ContentTitleForm,
  ContentText,
  FormContainer,
  ForgotPasswordLink,
} from '../styles';

import {
  InputLinksSection,
  InputLinksSectionUnderline,
  InputSection,
} from './styles';

function CanalReivindicado(props) {
  return (
    <ContentTitleForm>
      <Link to="/reivindicacao/buscarcanal">
        <ForgotPasswordLink>{'<voltar'}</ForgotPasswordLink>
      </Link>

      <FormContainer style={{ marginTop: 120 }}>
        <ContentText>
          <strong>Este canal já foi reivindicado!</strong>
          <br />
          {`Outro membro da equipe que tem acesso ao e-mail ${maskEmail(
            channelTest.itunes_owner_email
          )} já
            reivindicou este canal! Agora, somente ele pode te adicionar como
            membro da equipe.`}
          <br />
          <br />
          Entre em contato com ele(a)
        </ContentText>

        <InputSection>
          <InputLinksSection>Está com algum outro problema?</InputLinksSection>
          <Link to="/reivindicacao/help">
            <InputLinksSectionUnderline>
              {'Então entre em contato conosco>'}
            </InputLinksSectionUnderline>
          </Link>
        </InputSection>
      </FormContainer>
    </ContentTitleForm>
  );
}

export default CanalReivindicado;

