import styled from 'styled-components';

export const Container = styled.div`
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  align-items: center;
  justify-content: center;
  width: -webkit-fill-available;
  height: -webkit-fill-available;
  background-color: rgba(0, 0, 0, 0.4);
  display: ${({ isOpen }) => (isOpen ? 'flex' : 'none')};
`;

export const Content = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  box-shadow: 0 4px 16px 0 rgba(75, 0, 93, 0.4);
  background-color: #ffffff;
  padding-top: 44px;
  padding-bottom: 32px;
  padding-left: 52px;
  padding-right: 52px;
  max-width: 322px;
  min-width: 271px;
`;

export const Title = styled.p`
  margin: 0;
  font-family: 'Poppins';
  font-size: 20px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: center;
  color: #90278e;
  margin-bottom: 32px;
`;

export const Description = styled.p`
  margin: 0;
  font-family: 'Poppins';
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: 0.24px;
  text-align: center;
  color: #000000;
  margin-bottom: 32px;
`;

export const CloseModalText = styled.p`
  margin: 0;
  font-family: 'Poppins';
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: center;
  color: #8800a8;
  cursor: pointer;
  margin-top: 32px;
`;
