import React, { useState, useContext } from 'react';

import { Email } from '@material-ui/icons';

import {
  InviteSectionItem,
  InviteSectionItemInput,
  InviteButton,
} from './styles';

import CustomInput from '../../../../../../../components/CustomInput';
import { useUnform } from '../../../../../../../modules/unform.module';

import { SendInvite } from '../../../../../../../repositories/ChannelCasterInvite.respository';
import { emailValidation } from '../../../../../../../utils/email.utils';

import { CastersContext } from '../../../../casters.context';

function InviteSectionContainer() {
  const randomId = Math.random().toPrecision(15).replace('.', '');

  const [inputDisabled, toggleInputDisabled] = useState(true);
  const [buttonText, setButtonText] = useState('Enviar convite');
  const [email, setEmail] = useState('');

  const { appendInvitedList } = useContext(CastersContext);

  /**
   * 0 = not invited
   * 1 = sending
   * 3 = sended
   * 4 = any error
   */
  const [inviteStatus, toggleInviteStatus] = useState(0);

  const { Form, UnformInput } = useUnform();

  return (
    <Form onSubmit={(data) => {}}>
      <InviteSectionItem>
        <InviteSectionItemInput>
          <UnformInput
            label="E-mail"
            placeholder="E-mail"
            id={`'invited_email_${randomId}'`}
            name={`'invited_email_${randomId}'`}
            style={{ color: '#bc79cc' }}
            iconStart={<Email />}
            contentStyle={{ margin: 0 }}
            type="email"
            onChangeCallback={handleInputType}
            readOnly={inviteStatus === 3}
            value={email}
          />
        </InviteSectionItemInput>
        <InviteButton
          {...{ invited: inviteStatus === 3, error: inviteStatus === 4 }}
          onClick={() => handleSendInvite(email)}
          disabled={inputDisabled}
        >
          {buttonText}
        </InviteButton>
      </InviteSectionItem>
    </Form>
  );

  function handleInputType(email) {
    setEmail(email);
    if (emailValidation(email)) {
      toggleInputDisabled(false);
    } else {
      toggleInputDisabled(true);
    }
  }

  function handleSendInvite(email, channelId) {
    if (inviteStatus !== 0) return;

    setButtonText('Enviando...');
    toggleInviteStatus(2);
    SendInvite(email, channelId)
      .then(() => {
        toggleInviteStatus(3);
        setButtonText('Enviado');
        appendInvitedList({
          email,
          _id: `${Math.random().toPrecision(12).replace('.', '')}`,
        });
      })
      .catch(() => toggleInviteStatus(4));
  }
}

export default React.memo(InviteSectionContainer);
