import styled, { css } from 'styled-components';

export const Container = styled.div``;

export const SocialMediaItem = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`;

export const CheckboxLabel = styled.p`
  margin: 0;
  padding: 0;
  font-family: 'Poppins';
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: 0.24px;
  text-align: left;
  color: #000000;
  margin-right: 24px;
  min-width: 100px;
  max-width: 100px;
`;

const SocialMediaItemEnabledCss = css`
  opacity: 0.32;
`;

export const SocialMediaItemContent = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;

  ${({ able }) => able && SocialMediaItemEnabledCss}
`;
