/* eslint-disable no-unused-vars */
import React, { useState } from 'react';

import { Link } from 'react-router-dom';

import {
  ContentTitleForm,
  ForgotPasswordLink,
  FormContainer,
  InputContainer,
  ButtonSingIn,
  ContentText,
} from '../styles';

import {
  TextBadge,
  Badge,
  InputLinksSection,
  InputLinksSectionUnderline,
  Footer,
} from './styles';

function ErroSelecionarCanal({ history, match }) {
  const checkedWhite = require('../../../assets/png/Grupo 2439.png');
  const { push } = history;

  console.log(match);

  const [error, toggleError] = useState(match.params.error);

  return (
    <ContentTitleForm>
      <ForgotPasswordLink
        onClick={() => {
          push('/reivindicacao/buscarcanal');
        }}
      >
        {'<voltar'}
      </ForgotPasswordLink>

      <FormContainer style={{ paddingTop: 86 }}>
        <InputContainer>
          <Badge>
            <img
              style={{ marginRight: 24 }}
              src={checkedWhite}
              alt="checkedWhite.png"
            />
            <TextBadge>Não encontramos um e-mail</TextBadge>
          </Badge>

          {handlingText(error)}
        </InputContainer>

        <Footer>
          <ButtonSingIn maxWidth onClick={() => toggleError('2')}>
            Tentar novamente
          </ButtonSingIn>

          {error === '2' && (
            <>
              <InputLinksSection>Ainda não conseguiu?</InputLinksSection>
              <Link to="/reivindicacao/help">
                <InputLinksSectionUnderline>
                  {'Então entre em contato conosco>'}
                </InputLinksSectionUnderline>
              </Link>
            </>
          )}
        </Footer>
      </FormContainer>
    </ContentTitleForm>
  );

  function handlingText(erro) {
    return (
      <ContentText>
        {erro === '0' // podcast selecionado nao tem email
          ? `Nosso robozinho não encontrou um endereço de email no código do seu
          feed, por favor vá no seu feed e insira algum email, só assim conseguimos
          verificar se você realmente é o dono de "${channelTest.title}”, quando fizer
          isso clique em tentar novamente.`
          : erro === '1' // podcast adicionado pela tela do feed mas não possui email
          ? `Seu podcast foi adicionado! Porém, nosso robozinho não encontrou um
          endereço de e-mail! Por favor, insira um e-mail no seu feed, só assim
          conseguiremos confirmar se você é o dono de "${channelTest.title}”.
          Depois que fizer isso, clique em “Tentar novamente”.`
          : erro === '2' // mesmo apos tentar novamente, o podcaste esta sem email
          ? `Nosso robozinho ainda não encontrou um endereço de email no código
          do seu feed, por favor vá no seu feed e insira algum email, só assim
          conseguimos verificar se você realmente é o dono de "${channelTest.title}”,
          quando fizer isso clique em tentar novamente.`
          : ' '}
      </ContentText>
    );
  }
}

export default ErroSelecionarCanal;

