import React, { useState } from 'react';

import {
  ContentDescription,
  ContentTitleForm,
  ContentPadding,
  ContentText,
  ContentFooterLink,
  ContentFooterText,
  ContentFooter,
  FeedbackSendNewLink,
} from './styles';

function AbraSuaConta({ match }) {
  const checked = require('../../../assets/png/Checked.png');
  const checkedBranco = require('../../../assets/png/Checked branco.png');
  const [newLinkSucess, setNewLinkSuccess] = useState(null);
  const [textLinkSuccess, toggleTextLinkSuccess] = useState('Pronto!');

  const { token } = match.params;

  return (
    <ContentPadding>
      <ContentTitleForm>
        <ContentDescription>{textLinkSuccess}</ContentDescription>

        <ContentText>
          Enviamos um link de verificação pra você.
          <br />
          Caso não esteja na sua caixa de entrada, verifique a caixa de spam.
        </ContentText>
      </ContentTitleForm>

      <ContentFooter>
        <ContentFooterText>
          Não chegou?
          <br />
          <ContentFooterLink
            onClick={() => {
              setNewLinkSuccess(true);
              toggleTextLinkSuccess('Reenviado!');
            }}
          >
            {'Reenviar link'}
          </ContentFooterLink>
        </ContentFooterText>

        {newLinkSucess !== null ? (
          newLinkSucess ? (
            <FeedbackSendNewLink background={'#62FFB1'}>
              <img style={{ marginRight: 8 }} src={checked} alt="Checked" />
              <ContentFooterText>Feito!</ContentFooterText>
            </FeedbackSendNewLink>
          ) : (
            <FeedbackSendNewLink background={'#FF2727'}>
              <img
                style={{ marginRight: 8 }}
                src={checkedBranco}
                alt="Checked branco"
              />
              <ContentFooterText color="#fff">Erro :(</ContentFooterText>
            </FeedbackSendNewLink>
          )
        ) : (
          <> </>
        )}
      </ContentFooter>
    </ContentPadding>
  );
}

export default React.memo(AbraSuaConta);
