import React from 'react';

import { Container, ComingSoonText, ComingSoonImage } from './styles';

import ComingSoonImageSrc from '../../../assets/png/il_Bem-vindo.png';

function ComingSoonScreen() {
  return (
    <Container>
      <ComingSoonImage src={ComingSoonImageSrc} alt="comminsoon.png" />
      <ComingSoonText>Em breve</ComingSoonText>
    </Container>
  );
}

export default ComingSoonScreen;
