import styled, { css } from 'styled-components';

const AdPreviewOpacity = css`
  opacity: 0.2;
  filter: alpha(opacity=20);
`;

export const Container = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 44px;
`;

export const AdViewFeedbackContainer = styled.div`
  width: fit-content;
`;

export const AdViewFeedback = styled.div`
  background-color: #180024;
  ${({ status }) => status === 'Desativado' && AdPreviewOpacity};
  display: flex;
  flex-direction: row;
  width: 320px;
  padding: 21px 16px 16px 16px;
`;

export const AdFeedbackInformationContainer = styled.div`
  display: flex;
  flex-direction: column;
  padding-left: 16px;
  flex: 1;
  max-width: 194px;
  min-width: 194px;
  overflow: hidden;
`;

export const AdFeedbackLinkContainer = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
`;

export const AdFeedbackLinkIcon = styled.img`
  width: 14px;
  height: 14px;
`;

export const AdFeedbackLink = styled.p`
  margin: 0;
  padding: 0;
  font-family: 'Roboto';
  font-size: 14px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.36;
  letter-spacing: normal;
  text-align: right;
  color: #a74cd5;
  margin-right: 4px;
`;

export const AdFeedbackTitle = styled.p`
  margin: 0;
  padding: 0;
  height: 20px;
  font-family: 'Roboto';
  font-size: 14px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.14;
  letter-spacing: 0.54px;
  text-align: left;
  color: #ffffff;
  margin-bottom: 8px;
`;

export const AdFeedbackDescription = styled.p`
  margin: 0;
  padding: 0;
  font-family: 'Roboto';
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.33;
  letter-spacing: normal;
  text-align: left;
  color: #ffffff;
  height: 64px;
  max-height: 64px;
  overflow: hidden;
  margin-bottom: 4px;
  word-break: break-word;
`;

export const AdImageView = styled.div`
  height: 112px;
  width: 112px;
  background-color: rgba(255, 255, 255, 0.3);
`;

export const AdFeedbackImage = styled.img`
  height: auto;
  width: 112px;
  max-height: 112px;
`;

export const AdInformationContainer = styled.div`
  width: 200px;
  height: 152px;
  max-height: 152px;
  padding-left: 30px;
  overflow: hidden;
`;

export const AdInformationItem = styled.div`
  margin-bottom: 16px;
`;

export const AdInformationLabel = styled.p`
  margin: 0;
  padding: 0;
  font-family: Poppins;
  font-size: 12px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #72008d;
  text-transform: uppercase;
  margin-right: 8px;
`;

export const AdInformationLabelContainer = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`;

export const AdInformationData = styled.p`
  margin: 0;
  padding: 0;
  font-family: 'Poppins';
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 2;
  letter-spacing: 0.24px;
  text-align: left;
  color: #000000;
`;

export const AdHandlersContainer = styled.div`
  display: flex;
  align-items: flex-end;
  height: 152px;
`;

export const AdHandlerLink = styled.p`
  margin: 0;
  padding: 0;
  font-family: Poppins;
  font-size: 12px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: 0.24px;
  text-align: left;
  color: #90278e;
  text-decoration-line: underline;
  height: 18px;
  cursor: pointer;
`;
