import React, { useState, useEffect, useContext, createContext } from 'react';

import api from '../services/api.service';

import * as authServices from '../services/auth.service';

import jwtDecode from 'jwt-decode';

const AuthContext = createContext({});

function AuthProvider({ children }) {
  const [user, setUser] = useState(
    JSON.parse(localStorage.getItem('@CHAuth:user'))
  );
  const [token, setToken] = useState(localStorage.getItem('@CHAuth:token'));
  const [sameUserToken, setSameUserToken] = useState(isSameUserToken());

  function singIn(userParams) {
    return new Promise((resolve, reject) => {
      authServices
        .signIn(userParams)
        .then(({ data: { user, token } }) => {
          setUser(user);
          setToken(token);

          localStorage.setItem('@CHAuth:user', JSON.stringify(user));
          localStorage.setItem('@CHAuth:token', token);

          resolve(true);
        })
        .catch((err) => reject(err));
    });
  }

  function singUp(userParams) {
    return authServices.signUp(userParams);
  }

  async function signOut() {
    setUser(null);
    setToken(null);
    localStorage.clear();
  }

  useEffect(() => {
    setSameUserToken(isSameUserToken());
  }, [user, token]);

  function isSameUserToken() {
    if (user && token) {
      try {
        const { _id } = user;
        const decoded = jwtDecode(token);

        return decoded.id === _id;
      } catch (error) {
        return false;
      }
    }

    return false;
  }

  useEffect(() => {
    api.interceptors.request.use(async function (config) {
      const authorization = `Bearer ${token}`;
      config.headers.Authorization = authorization;

      return config;
    });
  }, [token]);

  return (
    <AuthContext.Provider
      value={{
        signed: Boolean(user),
        user,
        sameUserToken,
        singIn,
        signOut,
        singUp,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
}

export function useAuth() {
  const context = useContext(AuthContext);

  return context;
}

export default AuthProvider;
