import React from 'react';

import {
  Container,
  CardContainer,
  FollowersCard,
  MediumCard,
  CardHeader,
  CardTitle,
  MediumImg,
  FollowersCardImageContainer,
  FollowersImage,
  FollowersDescriptionSection,
  FollowersCardLabel,
  FollowersCardData,
  MediumCardSubTitle,
  MediumLinks,
  MediumLinksItem,
  MediumLinksStyle,
  HomeLink,
} from './styles';

import MediumIcon from '../../../assets/png/Grupo 2745.png';
import Microphone from '../../../assets/png/il_adicionar_podcast.png';

function Home() {
  return (
    <Container>
      <CardContainer>
        <FollowersCard>
          <CardHeader>
            <CardTitle>Assinantes</CardTitle>
          </CardHeader>
          <FollowersCardImageContainer>
            <FollowersImage src={Microphone} alt="" srcSet="" />
          </FollowersCardImageContainer>

          <FollowersDescriptionSection>
            <FollowersCardLabel>Seu canal tem </FollowersCardLabel>
            <FollowersCardData>3 mil assinantes</FollowersCardData>
          </FollowersDescriptionSection>
        </FollowersCard>

        <MediumCard>
          <CardHeader>
            <CardTitle>Nossos tutoriais</CardTitle>

            <a
              href="https://medium.com/casthood"
              target="_blank"
              rel="noopener noreferrer"
            >
              <MediumImg src={MediumIcon} alt="" srcSet="" />
            </a>
          </CardHeader>

          <MediumCardSubTitle>Me vê um podcast no capricho</MediumCardSubTitle>

          <MediumLinks>
            <MediumLinksItem>
              <HomeLink href="https://medium.com/casthood/me-v%C3%AA-um-podcast-no-capricho-parte-1-5-eeee627cfaad">
                <MediumLinksStyle>Parte 1 - começando</MediumLinksStyle>
                <MediumLinksStyle>{'>'}</MediumLinksStyle>
              </HomeLink>
            </MediumLinksItem>

            <MediumLinksItem>
              <HomeLink href="https://medium.com/casthood/como-gravar-um-podcast-cb528849f6c1">
                <MediumLinksStyle>Parte 2 - equipamentos</MediumLinksStyle>
                <MediumLinksStyle>{'>'}</MediumLinksStyle>
              </HomeLink>
            </MediumLinksItem>

            <MediumLinksItem>
              <HomeLink href="https://medium.com/casthood/edi%C3%A7%C3%A3o-de-podcast-t%C3%A9cnicas-supimpas-c7485d9a13a4">
                <MediumLinksStyle>Parte 3 - gravando</MediumLinksStyle>
                <MediumLinksStyle>{'>'}</MediumLinksStyle>
              </HomeLink>
            </MediumLinksItem>

            <MediumLinksItem>
              <HomeLink href="https://medium.com/casthood/como-eu-publico-e-fico-famoso-45e043c89c90">
                <MediumLinksStyle>Parte 4 - como fico famoso?</MediumLinksStyle>
                <MediumLinksStyle>{'>'}</MediumLinksStyle>
              </HomeLink>
            </MediumLinksItem>
            <MediumLinksItem>
              <HomeLink href="https://medium.com/casthood/criar-sua-comunidade-e-engajar-b0d3c85c38e">
                <MediumLinksStyle>Parte 5 - minha comunidade</MediumLinksStyle>
                <MediumLinksStyle>{'>'}</MediumLinksStyle>
              </HomeLink>
            </MediumLinksItem>
          </MediumLinks>
        </MediumCard>
      </CardContainer>
    </Container>
  );
}

export default React.memo(Home);
