import styled from 'styled-components';

export const SectionTitle = styled.p`
  font-family: 'Poppins';
  font-weight: bold;
  font-size: 13px;
  color: #72008d;
  text-align: left;
  letter-spacing: 0px;
  color: #72008d;
  text-transform: uppercase;
  margin-bottom: 16px;
  line-height: 20px;
`;

export const SectionLabel = styled.p`
  margin: 0;
  font-family: 'Poppins';
  font-size: 14px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #90278e;
  margin-bottom: 16px;
`;

export const CustomButton = styled.button.attrs((props) => ({
  type: props.type || 'button',
}))`
  font-family: 'Poppins';
  display: block;
  font-weight: 700;
  color: #fff;
  font-size: 20px;
  background: #72008d;
  padding: 10px 68px;
  /* width: 391px; */
  /* height: 48px; */
  border-radius: 56px;
  /* margin-top: 48px; */
  outline: none;
  border: 0;
  width: ${({ maxWidth }) => (maxWidth ? '-webkit-fill-available' : '300px')};

  &:disabled {
    background: #bfbfbf;
    color: #e3e3e3;
  }

  &:disabled:hover {
    cursor: not-allowed;
    background: #bfbfbf;
    color: #e3e3e3;
  }

  &:hover {
    cursor: pointer;
    background-color: #90278e;
  }
`;
