import React, { useState } from 'react';

import { InputAdornment, IconButton } from '@material-ui/core';
import { Visibility, VisibilityOff } from '@material-ui/icons';

import {
  ContentDescription,
  ButtonSingIn,
  ContentTitleForm,
  Form,
  FormInput,
  ContentPadding,
  ContentText,
  FeedbackSendNewLink,
  DivFeedBack,
  ContentFooterText,
} from './styles';

import TextInputReivindicar from '../../../components/TextInputReivindicar';

function NovaSenha({ history }) {
  const checked = require('../../../assets/png/Checked.png');

  const { push } = history;

  const [visibility, toggleVisibility] = useState(false);
  const [sendNewPassword, setSendNewPassword] = useState(false);

  return (
    <ContentPadding>
      <ContentTitleForm>
        <ContentDescription>Nova senha</ContentDescription>
        <ContentText>
          Crie uma nova senha, e lembre-se que ela deve ter no mínimo 8
          caracteres, sendo ao menos um deles um número.
        </ContentText>

        {!sendNewPassword ? FormNewPassword() : DivSendNewPassword()}
      </ContentTitleForm>
    </ContentPadding>
  );

  function FormNewPassword() {
    return (
      <Form onSubmit={() => setSendNewPassword(true)}>
        <FormInput>
          <TextInputReivindicar
            fullWidth
            id="input-passwd-1"
            label="Nova senha"
            required
            color="#72008D"
            type={visibility ? 'text' : 'password'}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton onClick={() => toggleVisibility(!visibility)}>
                    {visibility ? <VisibilityOff /> : <Visibility />}
                  </IconButton>
                </InputAdornment>
              ),
            }}
          />
        </FormInput>

        <FormInput>
          <TextInputReivindicar
            fullWidth
            id="input-passw2"
            label="Repita a senha"
            required
            color="#72008D"
            type={visibility ? 'text' : 'password'}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton onClick={() => toggleVisibility(!visibility)}>
                    {visibility ? <VisibilityOff /> : <Visibility />}
                  </IconButton>
                </InputAdornment>
              ),
            }}
          />
        </FormInput>

        <ButtonSingIn top={80}>Enviar</ButtonSingIn>
      </Form>
    );
  }

  function DivSendNewPassword() {
    return (
      <>
        <DivFeedBack>
          <FeedbackSendNewLink background={'#62FFB1'}>
            <img style={{ marginRight: 8 }} src={checked} alt="Checked" />
            <ContentFooterText>Feito!</ContentFooterText>
          </FeedbackSendNewLink>

          <ContentText style={{ marginTop: 32 }}>
            Já pode fazer o login
          </ContentText>
        </DivFeedBack>

        <ButtonSingIn onClick={() => push('/auth/login')} top={73}>
          Fazer login
        </ButtonSingIn>
      </>
    );
  }
}

export default React.memo(NovaSenha);
