import styled from 'styled-components';

export const Container = styled.div`
  background: rgb(95, 0, 115);
  background: linear-gradient(180deg, #90278e 0%, #72008d 100%);
  padding: 0;
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
`;

export const Content = styled.div`
  background-color: #f0f0f0;
  width: 800px;
  height: 672px;
  border-radius: 20px;
  display: flex;
  flex-direction: row;
`;

export const ContentForm = styled.div`
  background-color: #fff;
  width: 100%;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  display: flex;
  flex-direction: column;
  padding-top: 48px;
`;

export const ContentTitle = styled.p`
  margin: 0;
  color: #72008d;
  font-family: 'Poppins';
  font-weight: 500;
  font-size: 14px;
  text-decoration: underline;
  text-align: left;
  margin-bottom: 64px;

  &:hover {
    cursor: pointer;
    color: #72008d;
  }
`;

export const ForgotPasswordLink = styled.p`
  margin: 0;
  color: #72008d;
  font-family: 'Poppins';
  font-weight: 500;
  font-size: 14px;
  text-decoration: underline;
  text-align: left;

  &:hover {
    cursor: pointer;
    color: #72008d;
  }
`;

export const ContentTitleForm = styled.div`
  display: flex;
  flex-direction: column;
  text-align: left;
  width: -webkit-fill-available;
  padding: 32px 63px 48px 48px;
`;

export const InputContainer = styled.div``;

export const ContentDescription = styled.p`
  margin: 0;
  color: #72008d;
  font-family: 'Poppins';
  font-weight: 700;
  font-size: 35px;
  margin-bottom: 40px;
`;

export const ContentText = styled.p`
  margin: 0;
  color: #333;
  font-family: 'Poppins';
  font-weight: 200;
  font-size: 16px;
  text-align: initial;
  margin-bottom: 80px;
  line-height: 24px;
  letter-spacing: 0.32px;
`;

export const ButtonSingIn = styled.button`
  font-family: 'Poppins';
  display: flex;
  font-weight: 700;
  color: #fff;
  font-size: 20px;
  background: #72008d;
  width: -webkit-fill-available;
  border-radius: 56px;
  padding: 7px 0;
  align-self: flex-end;
  justify-content: center;
  align-items: center;

  outline: none;

  &:hover {
    cursor: pointer;
    background-color: #90278e;
  }
`;

export const ContentFooterText = styled.p`
  margin: 0;
  color: #72008d;
  font-family: 'Poppins';
  font-weight: 600;
  font-size: 14px;
  text-align: center;
  line-height: 22px;
`;

export const ContentFooterLink = styled.a`
  color: #72008d;
  font-family: 'Poppins';
  font-weight: 600;
  font-size: 14px;
  text-align: center;
  text-decoration: underline;

  &:hover {
    cursor: pointer;
    color: #72008d;
  }
`;

export const ContentPadding = styled.div`
  margin-right: 80px;
  margin-left: 48px;
  padding-top: 16px;
`;

export const ContentTextImage = styled.p`
  margin: 0;
  color: #c365c1;
  font-family: 'Poppins';
  font-weight: 700;
  font-size: 35px;
  line-height: 53px;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 80px;
  width: 226px;
  height: 102px;
  top: 600px;
`;

export const ContentImage = styled.div`
  background-color: #f8e5ff;
  width: 100%;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 168px;
`;

export const Form = styled.form``;

export const FormInput = styled.div`
  margin-top: 32px;
  display: flex;
  flex-direction: row;
  margin-bottom: 8px;
`;

export const DivForgetPassword = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-top: 12px;
`;

export const FormContainer = styled.div`
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  height: 100%;
  align-items: center;
`;
