import React, { useContext } from 'react';

import { Container } from './styles';

import AdListRoot, { AdListContext } from './adList.context';

function AdList() {
  return (
    <AdListRoot>
      <AdListContainer />
    </AdListRoot>
  );
}

function AdListContainer() {
  const { component } = useContext(AdListContext);
  return <Container>{component}</Container>;
}

export default AdList;
