import React, { useEffect, useState } from 'react';

import PropTypes from 'prop-types';

import {
  Container,
  SocialMediaItem,
  CheckboxLabel,
  SocialMediaItemContent,
} from './styles';

/**
 * components
 */
import CustomCheckbox from '../CustomCheckBox';
import CustomInput from '../CustomInput';

function SocialMediasContainer(props) {
  const { onChangeCallback } = props;
  /**
   * state
   */
  const [checkboxesState, setCheckboxesState] = useState({
    facebook: {
      checked: false,
      value: '',
    },
    instagram: {
      checked: false,
      value: '',
    },
    twitter: {
      checked: false,
      value: '',
    },
    linkedin: {
      checked: false,
      value: '',
    },
    TikTok: {
      checked: false,
      value: '',
    },
  });

  useEffect(() => {
    onChangeCallback(checkboxesState);
  });

  return (
    <Container>
      <SocialMediaItem>
        <CustomCheckbox
          checked={checkboxesState.facebook.checked}
          onChangeCallback={(value) =>
            setCheckboxesState({
              ...checkboxesState,
              facebook: {
                ...checkboxesState.facebook,
                checked: value,
              },
            })
          }
        />

        <SocialMediaItemContent able={!checkboxesState.facebook.checked}>
          <CheckboxLabel>facebook.com/</CheckboxLabel>
          <CustomInput
            readOnly={!checkboxesState.facebook.checked}
            onChangeCallback={(value) =>
              setCheckboxesState({
                ...checkboxesState,
                facebook: {
                  ...checkboxesState.facebook,
                  value,
                },
              })
            }
            id="facebook-social-media"
            name="facebook-social-media"
            label=""
            value={checkboxesState.facebook.value}
            placeholder="facebook"
            style={{ color: '#bc79cc' }}
            fieldGroupStyle={{ top: 4, position: 'relative' }}
            contentStyle={{ margin: 0 }}
          />
        </SocialMediaItemContent>
      </SocialMediaItem>

      <SocialMediaItem>
        <CustomCheckbox
          checked={checkboxesState.instagram.checked}
          onChangeCallback={(value) =>
            setCheckboxesState({
              ...checkboxesState,
              instagram: {
                ...checkboxesState.instagram,
                checked: value,
              },
            })
          }
        />

        <SocialMediaItemContent able={!checkboxesState.instagram.checked}>
          <CheckboxLabel>instagram.com/</CheckboxLabel>
          <CustomInput
            readOnly={!checkboxesState.instagram.checked}
            onChangeCallback={(value) =>
              setCheckboxesState({
                ...checkboxesState,
                instagram: {
                  ...checkboxesState.instagram,
                  value,
                },
              })
            }
            id="instagram-social-media"
            name="instagram-social-media"
            label=""
            value={checkboxesState.instagram.value}
            placeholder="Instagram"
            style={{ color: '#bc79cc' }}
            fieldGroupStyle={{ top: 4, position: 'relative' }}
            contentStyle={{ margin: 0 }}
          />
        </SocialMediaItemContent>
      </SocialMediaItem>

      <SocialMediaItem>
        <CustomCheckbox
          checked={checkboxesState.twitter.checked}
          onChangeCallback={(value) =>
            setCheckboxesState({
              ...checkboxesState,
              twitter: {
                ...checkboxesState.twitter,
                checked: value,
              },
            })
          }
        />

        <SocialMediaItemContent able={!checkboxesState.twitter.checked}>
          <CheckboxLabel>twitter.com/</CheckboxLabel>
          <CustomInput
            readOnly={!checkboxesState.twitter.checked}
            onChangeCallback={(value) =>
              setCheckboxesState({
                ...checkboxesState,
                twitter: {
                  ...checkboxesState.twitter,
                  value,
                },
              })
            }
            id="twitter-social-media"
            name="twitter-social-media"
            label=""
            value={checkboxesState.twitter.value}
            placeholder="twitter"
            style={{ color: '#bc79cc' }}
            fieldGroupStyle={{ top: 4, position: 'relative' }}
            contentStyle={{ margin: 0 }}
          />
        </SocialMediaItemContent>
      </SocialMediaItem>

      <SocialMediaItem>
        <CustomCheckbox
          checked={checkboxesState.linkedin.checked}
          onChangeCallback={(value) =>
            setCheckboxesState({
              ...checkboxesState,
              linkedin: {
                ...checkboxesState.linkedin,
                checked: value,
              },
            })
          }
        />

        <SocialMediaItemContent able={!checkboxesState.linkedin.checked}>
          <CheckboxLabel>linkedin.com/</CheckboxLabel>
          <CustomInput
            readOnly={!checkboxesState.linkedin.checked}
            onChangeCallback={(value) =>
              setCheckboxesState({
                ...checkboxesState,
                linkedin: {
                  ...checkboxesState.linkedin,
                  value,
                },
              })
            }
            id="linkedin-social-media"
            name="linkedin-social-media"
            label=""
            value={checkboxesState.linkedin.value}
            placeholder="Linkedin"
            style={{ color: '#bc79cc' }}
            fieldGroupStyle={{ top: 4, position: 'relative' }}
            contentStyle={{ margin: 0 }}
          />
        </SocialMediaItemContent>
      </SocialMediaItem>

      <SocialMediaItem>
        <CustomCheckbox
          checked={checkboxesState.TikTok.checked}
          onChangeCallback={(value) =>
            setCheckboxesState({
              ...checkboxesState,
              TikTok: {
                ...checkboxesState.TikTok,
                checked: value,
              },
            })
          }
        />

        <SocialMediaItemContent able={!checkboxesState.TikTok.checked}>
          <CheckboxLabel>Usuário:</CheckboxLabel>
          <CustomInput
            readOnly={!checkboxesState.TikTok.checked}
            onChangeCallback={(value) =>
              setCheckboxesState({
                ...checkboxesState,
                TikTok: {
                  ...checkboxesState.TikTok,
                  value,
                },
              })
            }
            id="TikTok-social-media"
            name="TikTok-social-media"
            label=""
            value={checkboxesState.TikTok.value}
            placeholder="TikTok"
            style={{ color: '#bc79cc' }}
            fieldGroupStyle={{ top: 4, position: 'relative' }}
            contentStyle={{ margin: 0 }}
          />
        </SocialMediaItemContent>
      </SocialMediaItem>
    </Container>
  );
}

SocialMediasContainer.propTypes = {
  onChangeCallback: PropTypes.func.isRequired,
};

SocialMediasContainer.defaultProps = {
  onChangeCallback: () => {},
};

export default React.memo(SocialMediasContainer);
