import './styles.css';
import React, { useState, useEffect, useRef } from 'react';

/**
 * Modules
 */
import { useField } from '@unform/core';

import PropTypes from 'prop-types';

import Tooltip from '@material-ui/core/Tooltip';

import { withStyles } from '@material-ui/core/styles';

import { TextAreaLengthText, IconContainer } from './styles';

import TooltipImage from '../../assets/png/Grupo 2748.png';

const LightTooltip = withStyles((theme) => ({
  tooltip: {
    // backgroundColor: theme.palette.common.white,
    color: 'rgba(0, 0, 0, 0.87)',
    fontSize: 12,
    background: '#FFFFFF 0% 0% no-repeat padding-box',
    boxShadow: '0px 0px 6px #00000067',
    fontFamily: 'Poppins',
    letterSpacing: '0.24px',
    padding: 16,
  },
}))(Tooltip);

export default function CustomInput(props) {
  const {
    name,
    id,
    placeholder,
    label,
    disabled,
    value,
    style,
    tooltip,
    tooltipLocation,
    readOnly,
    contentStyle,
    fieldGroupStyle,
    onChangeCallback,
    onKeyDownCallback,
    maxLength,
    iconStart,
  } = props;

  const [textLength, setTextLength] = useState(value ? value.length : 0);

  const inputRef = useRef(null);
  const { fieldName, defaultValue, registerField, error } = useField(name);

  useEffect(() => {
    registerField({
      name: fieldName,
      ref: inputRef.current,
      path: 'value',
    });
  }, [fieldName, registerField]);

  return (
    <>
      <div className="content" style={{ ...contentStyle }}>
        <div
          style={{ ...fieldGroupStyle }}
          className={`field-group ${disabled ? 'border-bottom-none' : ''}`}
        >
          {iconStart && (
            <IconContainer error={Boolean(error)} style={{ marginRight: 22 }}>
              {iconStart}
            </IconContainer>
          )}
          <div style={disabled && { borderBottomWidth: 0 }} className="field">
            <textarea
              ref={inputRef}
              className="input"
              onChange={({ target: { value } }) => {
                onChangeCallback(value);
                setTextLength(value.length);
              }}
              disabled={disabled}
              readOnly={readOnly}
              name={name}
              id={id}
              placeholder={placeholder}
              style={style}
              value={value}
              onKeyDown={({ target: { value }, key }) => {
                onKeyDownCallback({ value, key });
              }}
              maxLength={maxLength}
            />

            <label style={style} htmlFor={name}>
              {label}
            </label>
          </div>
        </div>

        {tooltip && (
          <div
            style={{
              position: 'relative',
              top: -24,
              left: '102%',
              width: 40,
              height: 40,
              margin: 0,
              padding: 0,
            }}
          >
            <LightTooltip title={tooltip} placement={tooltipLocation}>
              <img
                style={{ width: 16, height: 16 }}
                src={TooltipImage}
                alt=""
                srcSet=""
              />
            </LightTooltip>
          </div>
        )}
      </div>
      <div style={{ paddingTop: 0 }}>
        <TextAreaLengthText>{`${textLength}/${maxLength}`}</TextAreaLengthText>
      </div>
    </>
  );
}

CustomInput.propTypes = {
  type: PropTypes.oneOf(['email', 'text', 'password']),
  name: PropTypes.string,
  id: PropTypes.string,
  placeholder: PropTypes.string,
  label: PropTypes.string,
  disabled: PropTypes.bool,
  value: PropTypes.string,
  style: PropTypes.object,
  contentStyle: PropTypes.object,
  fieldGroupStyle: PropTypes.object,
  tooltip: PropTypes.string,
  tooltipLocation: PropTypes.oneOf(['bottom-end', 'bottom', 'bottom-start']),
  readOnly: PropTypes.bool,
  onChangeCallback: PropTypes.func.isRequired,
  onKeyDownCallback: PropTypes.func,
  maxLength: PropTypes.number,
  iconStart: PropTypes.node,
};

CustomInput.defaultProps = {
  contentStyle: {},
  fieldGroupStyle: {},
  onChangeCallback: () => {},
  onKeyDownCallback: () => {},
  maxLength: 150,
};
