import styled from 'styled-components';

export const Container = styled.div`
  padding: 0;
  width: -webkit-fill-available;
  background: #fff;
  border-radius: 10px;
  margin-bottom: 60px;
  min-width: 665px;
  min-height: 440px;
  padding-bottom: 40px;
`;

export const CardTitle = styled.p`
  font-size: 34px;
  font-family: 'Poppins';
  font-weight: bold;
  text-align: left;
  line-height: 42px;
  color: #ffffff;
  margin: 0px;
  margin-bottom: 17px;
`;

export const ImageDiv = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-bottom: 32px;
  padding-right: 48px;
`;

export const CardImage = styled.img`
  margin-top: -124px;
`;
