import RecuperarSenha from '../pages/Auth/RecuperarSenha';
import Login from '../pages/Auth/Login';
// import Home from '../pages/Auth/Home';
import AbraSuaConta from '../pages/Auth/AbraSuaConta';
import JoinSuccess from '../pages/Auth/JoinSuccess';
import ForgotPasswordSuccess from '../pages/Auth/ForgotPasswordSuccess';
import NovaSenha from '../pages/Auth/NovaSenha';

import AuthRoutesImage from '../assets/png/Grupo 2277.png';
import AuthRoutesSentEmailImage from '../assets/png/Grupo 2278.png';

import AuthRoutesImagePassword from '../assets/png/il_senha.png';

export default function FluxoReivindicacao() {
  return [
    {
      path: '/signup',
      layout: '/auth',
      Component: AbraSuaConta,
      image: AuthRoutesImage,
      title: 'Entre pro CastHood',
    },
    {
      path: '/signup/success/:token',
      layout: '/auth',
      Component: JoinSuccess,
      image: AuthRoutesSentEmailImage,
      title: 'Verifique seu e-mail',
    },
    {
      path: '/resetpassword/:token',
      layout: '/auth',
      Component: NovaSenha,
      image: AuthRoutesImagePassword,
      title: 'Criar nova senha',
    },
    {
      path: '/login',
      layout: '/auth',
      Component: Login,
      image: AuthRoutesImage,
      title: 'Entre pro CastHood',
    },
    {
      path: '/forgotpassword',
      layout: '/auth',
      Component: RecuperarSenha,
      image: AuthRoutesImage,
      title: 'Entre pro CastHood',
    },
    {
      path: '/forgotpassword/success/:token',
      layout: '/auth',
      Component: ForgotPasswordSuccess,
      image: AuthRoutesSentEmailImage,
      title: 'Verifique seu e-mail',
    },
  ];
}
