import React, { useState } from 'react';

import {
  Container,
  Header,
  Content,
  ProfileSection,
  ImageContainer,
  InputSection,
  HeaderContent,
  FileSelector,
  FileSelectorStyle,
  SocialMediasCoteiner,
  UserAvatar,
  UserAvatarDiv,
} from './styles';

import {
  SectionTitle,
  CustomButton,
} from '../../../components/GerenciadorCanais';
import CustomDropdown from '../../../components/CustomDropdown';
import CustomAccodion from '../../../components/CustomAccordion';
import CustomTextArea from '../../../components/CustomTextArea';
import SocialMediasContainer from '../../../components/SocialMediasContainer';

import DefaultProfileImage from '../../../assets/png/img_perfil.png';

import { useUnform } from '../../../modules/unform.module';

function MeuPerfil() {
  const [imageSrc, setImageSrc] = useState(DefaultProfileImage);

  const readFile = ({ target }) => {
    if (!target.files[0]) return;
    setImageSrc(URL.createObjectURL(target.files[0]));
  };

  const { Form, UnformInput } = useUnform();

  return (
    <Form onSubmit={(data) => {}}>
      <Container>
        <SectionTitle>Sua atuação neste canal</SectionTitle>
        <Header>
          <HeaderContent>
            <CustomDropdown
              name={'rolechannel'}
              id={'rolechannel'}
              label={'Cargo'}
              placeholder={'Cargo'}
              tooltip={
                'Cada canal que você participa você pode colocar uma atuação diferente'
              }
              tooltipLocation={'right-start'}
              options={['Apresentador', 'Moderador', 'Editor']}
              onChangeCallback={(value) => {}}
            />
          </HeaderContent>
          {/* <LightTooltip
          title="Cada canal que você participa você pode colocar uma atuação diferente"
          placement="right-start"
          >
          <img
          style={{ marginLeft: 8, width: 16, height: 16 }}
          src={TooltipImage}
          alt=""
          srcSet=""
          />
        </LightTooltip> */}
        </Header>

        <Content>
          <SectionTitle>Seu perfil</SectionTitle>

          <ProfileSection>
            <ImageContainer>
              <UserAvatarDiv>
                <UserAvatar src={imageSrc} alt="" srcSet="" />
              </UserAvatarDiv>
              <FileSelectorStyle>
                <FileSelector
                  id="car"
                  type="file"
                  accept="image/*"
                  capture="camera"
                  onChange={readFile}
                />
                Adicionar foto
              </FileSelectorStyle>

              {/* <span>Adicionar foto</span> */}
            </ImageContainer>
            <InputSection>
              <UnformInput
                style={{ color: '#bc79cc' }}
                id="username"
                name="username"
                placeholder="Nome artístico"
                type="text"
                // value="Rodrigo Suman"
                label="Nome artístico"
                tooltip="Pode ser seu nome mesmo, só o sobrenome, apelido, alter ego, nick do msn, tanto faz, é a forma como você é conhecido(a)"
                tooltipLocation="bottom-start"
              />

              <CustomDropdown
                name={'mygenere'}
                id={'mygenere'}
                label={'Gênero'}
                placeholder={'Gênero'}
                options={['Masculino', 'Feminino', 'Não-binário', 'Outro']}
                onChangeCallback={(value) => {}}
              />

              <CustomTextArea
                style={{ color: '#bc79cc' }}
                id="userbio"
                name="userbio"
                placeholder="Bio"
                type="text"
                label="Bio"
                maxLength={1000}
                // onChangeCallback={(value) => setSelfDescription(value)}
              />
            </InputSection>
          </ProfileSection>

          <SocialMediasCoteiner>
            <CustomAccodion
              description="Se quiser ser seguido por aí já deixa os links tudo!"
              title="Redes sociais"
            >
              <SocialMediasContainer />
            </CustomAccodion>
          </SocialMediasCoteiner>
        </Content>

        <CustomButton>Salvar</CustomButton>
      </Container>
    </Form>
  );
}

export default MeuPerfil;
