import React, { createContext, useState } from 'react';
import PropTypes from 'prop-types';

export const AdContext = createContext({});

export default function AdScreenContext(props) {
  const { children } = props;

  /**
   * states
   */
  const [haveReadTheTerms, toggleHaveReadTheTerms] = useState(false);
  const [invitedTheTerms, toggleInvitedTheTerms] = useState(false);

  function handleToggleHaveReadTheTerms() {
    toggleHaveReadTheTerms((prev) => !prev);
  }

  function handleToggleInvitedTheTerms() {
    toggleInvitedTheTerms((prev) => !prev);
  }

  return (
    <AdContext.Provider
      value={{
        haveReadTheTerms,
        handleToggleHaveReadTheTerms,
        invitedTheTerms,
        handleToggleInvitedTheTerms,
      }}
    >
      {children}
    </AdContext.Provider>
  );
}

AdScreenContext.propTypes = {
  children: PropTypes.oneOfType([PropTypes.func, PropTypes.array]),
};
