import React from 'react';

import { Container, CardTitle, CardImage, ImageDiv } from './styles';

export default function Card({ children, cardTitle, image, altImage }) {
  return (
    <>
      <CardTitle>{cardTitle}</CardTitle>
      <Container>
        {image && (
          <ImageDiv>
            <CardImage src={image} alt={altImage} />
          </ImageDiv>
        )}
        {children}
      </Container>
    </>
  );
}
