import styled, { css } from 'styled-components';

const isDragActiveStyle = css`
  background-color: rgba(239, 219, 238);
  opacity: 0.53;
`;

export const FileUploadContent = styled.div`
  padding: 4px;
  border-radius: 10px;
  border: 4px solid #bc79cc;
  width: 290px;
  height: 175px;
  margin-right: 39px;

  ${({ isDragActive }) => isDragActive && isDragActiveStyle}
`;

export const FileUploadContentTwo = styled.div`
  border-radius: 10px;
  border: 2px dashed #bc79cc;
  height: -webkit-fill-available;
  width: -webkit-fill-available;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 48px 0 9px 0;
`;

export const FileUploadContainerTitle = styled.p`
  padding: 0;
  margin: 0;
  font-family: 'Poppins';
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.64;
  letter-spacing: 0.28px;
  text-align: center;
  color: #a817ae;
  margin-bottom: 22px;
`;

export const FindFilesButton = styled.button`
  outline: hidden;
  border-radius: 24px;
  box-shadow: 0 3px 6px 0 rgba(33, 0, 41, 0.3);
  background-color: #72008d;
  padding: 8px 18px;
  font-family: Poppins;
  font-size: 12px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 0.92;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
  border: 0;
  cursor: pointer;
  margin-bottom: 8px;

  &:hover {
    background-color: #90278e;
  }
`;

export const DescriptionContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 33px;
`;

export const FileUploadContainerDescription = styled.p`
  padding: 0;
  margin: 0;
  font-family: 'Poppins';
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.33;
  letter-spacing: 0.24px;
  text-align: center;
  color: #848484;

  p {
    margin: 0;
  }
`;
