import React, { useState } from 'react';

import { Person, RssFeed, Description, Radio } from '@material-ui/icons';

import {
  ContentTitleForm,
  ContentText,
  ForgotPasswordLink,
  FormContainer,
  InputContainer,
  ButtonSingIn,
} from '../styles';

import { Badge, BadgeText, SuccessContainer } from './styles';

import { useUnform } from '../../../modules/unform.module';

function FormHelp({ ...props }) {
  const checked = require('../../../assets/png/Checked.png');
  const [send, toggleSend] = useState(false);

  const { Form, UnformInput, UnformTextArea } = useUnform();

  const { history } = props;

  function handlingHelp(e) {
    toggleSend(!send);
  }

  return (
    <ContentTitleForm onSubmit={(e) => handlingHelp(e)}>
      <ForgotPasswordLink onClick={() => history.goBack()}>
        {'<voltar'}
      </ForgotPasswordLink>
      {send ? renderFeedback() : renderForm()}
    </ContentTitleForm>
  );

  function renderForm() {
    return (
      <Form onSubmit={(e) => handlingHelp(e)}>
        <FormContainer>
          <InputContainer style={{ paddingTop: 120 }}>
            <UnformInput
              id="user_name"
              name="user_name"
              label="Seu nome"
              placeholder="Seu nome"
              required
              type="text"
              iconStart={<Person />}
            />

            <UnformInput
              name="channel_name"
              id="channel_name"
              label="Nome do podcast"
              placeholder="Nome do podcast"
              required
              type="text"
              iconStart={<Radio />}
            />

            <UnformInput
              id="channel_rss"
              name="channel_rss"
              label="Feed RSS"
              placeholder="Feed RSS"
              required
              type="text"
              iconStart={<RssFeed />}
            />

            <UnformTextArea
              id="description"
              name="description"
              label="Descreva seu problema"
              placeholder="Descreva seu problema"
              required
              type="text"
              iconStart={<Description />}
              maxLength={600}
            />
          </InputContainer>

          <ButtonSingIn>Enviar</ButtonSingIn>
        </FormContainer>
      </Form>
    );
  }

  function renderFeedback() {
    return (
      <SuccessContainer>
        <Badge>
          <img src={checked} alt="checked.jpg" />
          <BadgeText>Enviado</BadgeText>
        </Badge>

        <ContentText>
          Fique tranquilo! Entraremos em contato o mais breve possível! Deixa
          com a gente!
        </ContentText>
      </SuccessContainer>
    );
  }
}

export default React.memo(FormHelp);
