import React from 'react';

import { Link } from 'react-router-dom';

import {
  ContentTitleForm,
  ContentText,
  FormContainer,
  InputContainer,
} from '../../../styles';

import {
  Badge,
  BadgeText,
  InputLinksSection,
  InputLinksSectionUnderline,
} from './styles';

function FeedbackEmail() {
  const checked = require('../../../../../assets/png/Checked.png');

  return (
    <ContentTitleForm>
      <FormContainer>
        <InputContainer
          style={{
            paddingTop: 203,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Badge>
            <img style={{ marginRight: 24 }} alt="checked.png" src={checked} />
            <BadgeText>Pronto</BadgeText>
          </Badge>

          <ContentText>
            {`Enviado! Verifique a caixa de entrada de podcasts40+10b00cec@anchor.fm.
                      Se não estiver na caixa de entrada, tente olhar na caixa de spam!`}
          </ContentText>
        </InputContainer>

        <div>
          <InputLinksSection>
            Não recebeu?
            <br />
            <Link to="/reivindicacao/help">
              <InputLinksSectionUnderline>
                Então entre em contato conosco
              </InputLinksSectionUnderline>
            </Link>
          </InputLinksSection>
        </div>
      </FormContainer>
    </ContentTitleForm>
  );
}

export default FeedbackEmail;
