import styled from 'styled-components';

/**
 * assets
 */
import MicrophoneImage from '../../../../assets/png/Microfone.png';

export const Container = styled.nav`
  height: 134px;
  width: -webkit-fill-available;
  min-width: 690px;
  background: #983999;
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  padding-left: 299px;
  padding-right: 32px;
  justify-content: space-between;
  z-index: 0;
  box-shadow: 0 4px 16px 0 rgba(75, 0, 93, 0.4);
`;

export const AppNameView = styled.div`
  height: 100%;
  display: flex;
  align-items: center;
`;

export const AppName = styled.p`
  font-family: 'Poppins';
  color: #f472a8;
  font-weight: bold;
  font-size: 20px;
  text-transform: uppercase;
  line-height: 42px;
  margin-left: -32px;
`;

export const Microphone = styled.img.attrs({
  src: MicrophoneImage,
  alt: 'Microfone.png',
})`
  width: 74px;
  height: 114px;
  align-self: flex-end;
`;
