import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';

import { Close, Search } from '@material-ui/icons';

import { Container, TagContainer, Tag, InputContainer } from './styles';

import CustomInput from '../../../../../components/CustomInput';

import { useSelector } from 'react-redux';

function TagsContainer(props) {
  const { channelActive } = useSelector(({ channelReducer }) => channelReducer);

  const { onChangeCallBack } = props;

  const [tagList, setTagList] = useState([]);
  const [inputValue, setInputValue] = useState('');

  useEffect(() => {
    onChangeCallBack(tagList);
  }, [tagList]);

  useEffect(() => {
    setTagList(channelActive.itunes_keywords || []);
  }, [channelActive]);

  function removeTagFromList(index) {
    let list = tagList.map((tag) => tag);

    list.splice(index, 1);

    setTagList(list);
  }

  function onKeyDownListener({ key, value }) {
    if (key === 'Enter') {
      setTagList([
        ...tagList,
        ...value
          .trim()
          .split(' ')
          .filter((item) => item.trim().length > 0),
      ]);

      setInputValue('');
    }
  }

  return (
    <Container>
      <InputContainer>
        <CustomInput
          iconStart={<Search />}
          label=""
          fieldGroupStyle={{ marginBottom: 0 }}
          contentStyle={{ marginBottom: 0 }}
          value={inputValue}
          placeholder="Procurar"
          onChangeCallback={(value) => setInputValue(value)}
          style={{ color: '#7e0098' }}
          name="input-search-channel-tag-section"
          id="input-search-channel-tag-section"
          onKeyDownCallback={onKeyDownListener}
          type="text"
        />
      </InputContainer>

      <TagContainer>
        {tagList.map((tag, key) => (
          <Tag key={key}>
            {tag}
            <Close
              style={{ fontSize: 14, marginLeft: 6, cursor: 'pointer' }}
              onClick={() => removeTagFromList(key)}
            />
          </Tag>
        ))}
      </TagContainer>
    </Container>
  );
}

TagsContainer.propTypes = {
  tags: PropTypes.array,
  onChangeCallBack: PropTypes.func,
};

TagsContainer.defaultProps = {
  tags: [],
  onChangeCallBack: () => {},
};

export default React.memo(TagsContainer);
