import styled from 'styled-components';

export const Container = styled.div`
  padding-left: 32px;
`;

export const Header = styled.div`
  padding-bottom: 32px;
  border-bottom: 1px dashed #72008d;
  margin-bottom: 32px;
  display: flex;
  flex-direction: row;
  align-items: center;
`;

export const HeaderContent = styled.div`
  width: 400px;
  display: flex;
  flex-direction: row;
  align-items: center;
`;

export const Content = styled.div`
  width: 552px;
  min-width: 552px;
  margin-bottom: 48px;
`;

export const ProfileSection = styled.div`
  display: flex;
  flex-direction: row;
`;

export const ImageContainer = styled.div`
  margin-right: 42px;
  display: flex;
  flex-direction: column;
  width: 150px;
  height: 250px;
  /* background-color: #000; */
  /* justify-content: space-between; */
`;

export const InputSection = styled.div`
  height: 250px;
  display: flex;
  /* justify-content: space-between; */
  flex-direction: column;
  width: -webkit-fill-available;
`;

export const UserAvatar = styled.img`
  width: inherit;
  height: auto;
  max-height: 200px;
  background: rgba(239, 219, 238, 0.53);
`;

export const UserAvatarDiv = styled.div`
  width: 150px;
  height: 200px;
  background: rgba(239, 219, 238, 0.53);
  display: flex;
  align-items: center;
  justify-content: center;
`;

export const Option = styled.p`
  display: flex;
  align-items: center;
  cursor: pointer;
  height: 48px;
  &:hover {
    background-color: rgba(0, 0, 0, 0.1);
  }
  margin: 0px;
  padding: 8px;
`;

export const FlexRow = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  flex: 1;
  justify-content: space-between;
  /* width: 100%; */
`;

export const FileSelector = styled.input.attrs({
  type: 'file',
  accept: 'image/*',
  capture: 'camera',
})`
  display: none;
`;

export const FileSelectorStyle = styled.label`
  font-family: 'Poppins';
  font-weight: bold;
  font-size: 12px;
  color: #fff;
  background-color: #8b2093;
  opacity: 0.34;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 4px;
  padding-bottom: 4px;
  cursor: pointer;
`;

export const SocialMediasCoteiner = styled.div`
  margin-top: 48px;
`;
