import React from 'react';

import {
  Container,
  Content,
  CastersListContainer,
  CasterListHeader,
  CasterInviteContainer,
  CasterInviteHeader,
  CasterInvitedContainer,
  CasterInvitedHeader,
} from './styles';

import { SectionLabel } from '../../../components/GerenciadorCanais';

import CastersList from './components/CastersList';
import InviteSection from './components/InviteSection';
import InvitedList from './components/InvitedList';

import CastersMainContext from './casters.context';

function Casters() {
  return (
    <Container>
      <Content>
        <CastersListContainer>
          <CasterListHeader>
            <SectionLabel>Ativos</SectionLabel>
          </CasterListHeader>

          <CastersList />
        </CastersListContainer>

        <CasterInviteContainer>
          <CasterInviteHeader>
            <SectionLabel>Convidar</SectionLabel>
          </CasterInviteHeader>

          <InviteSection />
        </CasterInviteContainer>

        <CasterInvitedContainer>
          <CasterInvitedHeader>
            <SectionLabel>Pendentes</SectionLabel>
          </CasterInvitedHeader>

          <InvitedList />
        </CasterInvitedContainer>
      </Content>
    </Container>
  );
}

function CastersContextContainer() {
  return (
    <CastersMainContext>
      <Casters />
    </CastersMainContext>
  );
}

export default CastersContextContainer;
