import React, { useContext } from 'react';

import {
  Container,
  AdListContainer,
  AdListContent,
  AdListFeedBackText,
} from './styles';

import { AdListContext } from '../../adList.context';

import { Title } from '../../../../styles';
import { CustomButton } from '../../../../../../../components/GerenciadorCanais';

import AdItem from '../AdItem';

function AdList() {
  const { setNewAdComponent } = useContext(AdListContext);
  return (
    <Container>
      <CustomButton onClick={setNewAdComponent}>Novo anúncio</CustomButton>

      <AdListContainer>
        <Title>Anúncio(S) CRIADO(S)</Title>
        <AdListContent>
          {/* <AdListFeedBackText>Não há nada aqui ainda.</AdListFeedBackText> */}

          <AdItem />
        </AdListContent>
      </AdListContainer>
    </Container>
  );
}

export default AdList;
