import styled from 'styled-components';

import { Link } from 'react-router-dom';

/**
 * assets
 */
import LogoCompleto from '../../../../assets/png/Logo_completo.png';

export const CustomLink = styled(Link)`
  font-family: 'Poppins';
  font-weight: ${({ focused }) => (focused ? 'bold' : 500)};
  color: ${({ focused }) => (focused ? '#72008D' : '#707070')};
  font-size: 18px;
  text-decoration: none;

  p {
    margin-bottom: 32px;
  }
`;

export const Container = styled.nav`
  height: 100vh;
  min-height: 100vh;
  background: linear-gradient(180deg, #ffffff 0%, #f7ecf7 100%);
  position: fixed;
  display: block;
  overflow-y: auto;
  left: 0;
  top: 0;
  padding: 0 32px 32px 32px;
  max-width: 200px !important;
  z-index: 10;
`;

export const Brand = styled.img.attrs({
  src: LogoCompleto,
  alt: 'Logo_completo.png',
})`
  width: 201;
  height: 34;
`;

export const BrandView = styled.div`
  display: flex;
  height: 134px;
  width: 100%;
  align-items: center;
  justify-content: center;
  background: transparent;
  margin-bottom: 26px;
`;
