import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { useDropzone } from 'react-dropzone';

import {
  FileUploadContent,
  FileUploadContentTwo,
  FileUploadContainerTitle,
  FindFilesButton,
  DescriptionContainer,
  FileUploadContainerDescription,
} from './styles';

function FileUpload(props) {
  const { onGetFileCallback, acceptedFiles, description } = props;

  const onDrop = useCallback((files) => {
    // Do something with the files
    const firstFile = files[0];

    const { type } = firstFile;

    const [mimeType] = type.split('/');

    if (mimeType !== acceptedFiles) {
      onGetFileCallback(true, undefined);
      return;
    }

    if (files[0]) {
      onGetFileCallback(false, firstFile);
      return;
    }
  }, []);
  const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });

  return (
    <FileUploadContent {...{ ...getRootProps(), isDragActive }}>
      <input {...getInputProps()} accept="image/*" />
      <FileUploadContentTwo>
        <FileUploadContainerTitle>
          Solte seu arquivo aqui ou
        </FileUploadContainerTitle>

        <FindFilesButton>Escolher arquivo</FindFilesButton>

        <DescriptionContainer>
          <FileUploadContainerDescription>
            {description}
          </FileUploadContainerDescription>
        </DescriptionContainer>
      </FileUploadContentTwo>
    </FileUploadContent>
  );
}

FileUpload.propTypes = {
  onGetFileCallback: PropTypes.func,
  description: PropTypes.node,
  acceptedFiles: PropTypes.oneOf(['image', 'audio']),
};

FileUpload.defaultProps = {
  onGetFileCallback: () => {},
  description: () => <p></p>,
  acceptedFiles: null,
};

export default React.memo(FileUpload);
