import styled from 'styled-components';

export const ChannelContainer = styled.div`
  display: flex;
  justify-content: center;
  flex-direction: column;
  box-shadow: 0px 1px 10px #bc79cc46;
  width: 267px;
  height: 223px;
  align-items: center;
  margin-top: 14px;
  margin-bottom: 27px;
`;

export const ChannelImage = styled.img`
  width: 86px;
  height: 86px;
  /* border-radius: 40px; */
  margin-bottom: 16px;
`;

export const ChannelTitle = styled.p`
  letter-spacing: 0px;
  color: #bc79cc;
  opacity: 1;
  font-family: 'Poppins';
  font-size: 21px;
  font-weight: bold;
  margin-bottom: 22px;
`;

export const ChannelEmail = styled.p`
  font-family: 'Poppins';
  font-weight: normal;
  text-align: left;
  font-size: 14px;
  letter-spacing: 0.28px;
  color: #6f6f6f;
  opacity: 1;
`;
