import React, { useState, useRef } from 'react';

/**
 * Modules
 */
import { Lock } from '@material-ui/icons';
import * as Yup from 'yup';

/**
 * styled-components
 */
import { Container, InputSection } from './styles';

/**
 * components
 */
import CustomModal from '../../../components/CustomModal';
import {
  SectionTitle,
  CustomButton,
} from '../../../components/GerenciadorCanais';

import { useUnform } from '../../../modules/unform.module';
import { useAuth } from '../../../contexts/auth.context';
import { changePassword } from '../../../services/user.service';

export default function Conta() {
  const [changePasswordModalOpened, toggleChangePasswordModalOpened] = useState(
    {
      value: false,
      title: '',
      description: '',
    }
  );

  const formRef = useRef(null);

  const { Form, UnformInput } = useUnform();

  const { user } = useAuth();

  function userName() {
    if (!user.first_name) return 'não informado';

    return `${user.first_name} ${user.last_name || ''}`;
  }

  return (
    <Form ref={formRef} onSubmit={handleSubmit}>
      <Container>
        <SectionTitle>Dados</SectionTitle>
        <InputSection>
          <UnformInput
            type="text"
            label="NOME COMPLETO"
            name="full_name"
            id="full_name"
            placeholder="Nome completo"
            disabled
            value={userName()}
          />
          <UnformInput
            type="email"
            label="E-MAIL"
            name="email"
            id="email"
            placeholder="E-mail"
            disabled
            value={user.email}
          />
        </InputSection>

        <SectionTitle>Redefinir senha</SectionTitle>
        <InputSection>
          <UnformInput
            style={{ color: '#bc79cc' }}
            type="password"
            label="Nova senha"
            name="password"
            id="password"
            placeholder="Nova senha"
            feedback="min. 6 caracteres contento letra(s) e número(s)"
            iconStart={<Lock />}
          />
          <UnformInput
            style={{ color: '#bc79cc' }}
            type="password"
            label="Repetir senha"
            name="password_confirmation"
            id="password_confirmation"
            placeholder="Repetir senha"
            iconStart={<Lock />}
          />
        </InputSection>
        <CustomButton maxWidth type="submit">
          Trocar senha
        </CustomButton>

        <CustomModal
          isOpen={changePasswordModalOpened.value}
          toggleModalOpen={toggleChangePasswordModalOpened}
          title={changePasswordModalOpened.title}
          closeModalText=""
          description={changePasswordModalOpened.description}
        >
          <CustomButton
            type="button"
            onClick={() =>
              toggleChangePasswordModalOpened({
                ...changePasswordModalOpened,
                value: false,
              })
            }
          >
            Ok
          </CustomButton>
        </CustomModal>
      </Container>
    </Form>
  );

  async function handleSubmit(data) {
    try {
      const schema = Yup.object().shape({
        password: Yup.string()
          .label('password')
          .min(8, '')
          .required('Este campo não pode ficar vazio')
          .matches(
            /^(?=.*[A-Za-z])(?=.*[0-9])(?=.{8,})/,
            'min. 6 caracteres contento letra(s) e número(s)'
          ),
        password_confirmation: Yup.string()
          .label('password_confirmation')
          .min(8, '')
          .required('Este campo não pode ficar vazio')
          .test('passwords-match', 'As senhas estão diferentes', function (
            value
          ) {
            return this.parent.password === value;
          }),
      });

      await schema.validate(data, {
        abortEarly: false,
      });
      // Validation passed

      const params = {
        userId: user._id,
        password: data.password,
      };

      changePassword(params)
        .then(() => {
          toggleChangePasswordModalOpened({
            value: true,
            title: 'Pronto!',
            description: 'Senha atualizada com sucesso! ',
          });
        })
        .catch(() => {
          toggleChangePasswordModalOpened({
            value: true,
            title: 'Erro!!!',
            description: 'Tivemos problemas ao alterar sua senha.',
          });
        });
    } catch (err) {
      if (err instanceof Yup.ValidationError) {
        const validationErrors = {};
        if (err instanceof Yup.ValidationError) {
          err.inner.forEach((error) => {
            validationErrors[error.path] = error.message;
          });
          formRef.current.setErrors(validationErrors);
        }
      }
    }
  }
}
