import React, { useState } from 'react';

import PropTypes from 'prop-types';

import './styles.css';
// import { Container } from './styles';

import SetaExpandir from '../../assets/png/seta-expandir.png';

function CustomAccordion(props) {
  const { title, children, borderBottom, description } = props;
  const [opened, toggleOpened] = useState(false);

  return (
    <div
      className={`accodion-container${borderBottom ? ' border-bottom' : ''}`}
    >
      <button
        type="button"
        onClick={() => toggleOpened(!opened)}
        className="accordion"
      >
        <div className="flex-row">
          {title}
          <img
            src={SetaExpandir}
            className={opened ? 'icon-opened' : 'icon-closed'}
            alt=""
            srcSet=""
            style={{
              width: 20,
              height: 11,
            }}
          />
        </div>
        {description && <p className="accordion-description">{description}</p>}
      </button>

      <div className={`panel accordion-content${opened ? ' active' : ''}`}>
        {children}
      </div>
    </div>
  );
}

export default CustomAccordion;

CustomAccordion.propTypes = {
  style: PropTypes.object,
  children: PropTypes.node,
  title: PropTypes.string.isRequired,
  description: PropTypes.string,
  borderBottom: PropTypes.bool,
};

CustomAccordion.defaultProps = {
  style: {},
  children: () => <></>,
  title: '',
  description: null,
  borderBottom: false,
};
