import React, { useContext } from 'react';

import { InviteSection, AddNewInvite } from './styles';

// import InviteItem from './components/InviteItem';

import InviteContext, { InviteSectionContext } from './inviteSection.context';

function InviteSectionContainer() {
  const { appendInviteList, inviteList } = useContext(InviteSectionContext);

  return (
    <InviteSection>
      {/* <InviteItem /> */}
      {inviteList.map((inviteItem) => inviteItem)}

      <AddNewInvite onClick={appendInviteList}>+ adicionar outro</AddNewInvite>
    </InviteSection>
  );
}

function InviteMainContext() {
  return (
    <InviteContext>
      <InviteSectionContainer />
    </InviteContext>
  );
}

export default React.memo(InviteMainContext);
