import React from 'react';

import { Email } from '@material-ui/icons';

import {
  ContentTitle,
  ContentDescription,
  ButtonSingIn,
  ContentTitleForm,
  ContentText,
  FormContainer,
  InputContainer,
} from '../styles';

import { useUnform } from '../../../modules/unform.module';

function RecuperarSenha({ history }) {
  const { goBack, push } = history;

  const { Form, UnformInput } = useUnform();

  return (
    <ContentTitleForm>
      <ContentTitle onClick={() => goBack()}>{'<voltar'}</ContentTitle>

      <ContentDescription>Esqueci a senha</ContentDescription>

      <ContentText style={{ marginBottom: 24 }}>
        Coloque seu e-mail e te enviaremos um link para criar uma nova senha.
      </ContentText>

      <Form onSubmit={handleForgotPassword}>
        <FormContainer>
          <InputContainer>
            <UnformInput
              fullWidth
              id="email"
              name="email"
              label="E-mail"
              placeholder="E-mail"
              style={{ color: '#BC79CC' }}
              type="email"
              iconStart={<Email />}
            />
          </InputContainer>

          <ButtonSingIn>Enviar</ButtonSingIn>
        </FormContainer>
      </Form>
    </ContentTitleForm>
  );

  function handleForgotPassword(data) {
    push(`/auth/forgotpassword/success/${data.email}`);
  }
}

export default React.memo(RecuperarSenha);
