import { takeLatest, put } from 'redux-saga/effects';
import PodcastsServices from '../../../services/Podcasts';

function* fetch() {
  const { getAll } = PodcastsServices;

  let response = yield getAll()
    .then(res => res.data)
    .catch(err => {});

  yield put({ type: 'FETCH_PODCASTS', payload: { list: response } });
}

function* toggle(action) {
  yield put({
    type: 'TOGGLE_PODCAST',
    payload: { podcast: action.payload },
  });
}

export default function* rootPodcasts() {
  yield takeLatest('ASYNC_FETCH_PODCASTS', fetch);
  yield takeLatest('ASYNC_TOGGLE_PODCAST', toggle);
}
