import styled from 'styled-components';

export const InputSection = styled.div`
  display: flex;
  flex-direction: column;
`;

export const InputDescriptionSection = styled.div`
  display: flex;
  flex-direction: row;
  width: -webkit-fill-available;
  justify-content: flex-end;
`;

export const InputLinksSection = styled.p`
  font-family: 'Poppins';
  font-size: 14px;
  text-align: right;
  font-weight: 600;
  letter-spacing: 0px;
  color: #72008d;
  opacity: 1;
`;

export const InputLinksSectionUnderline = styled.p`
  font-family: 'Poppins';
  font-size: 14px;
  text-align: right;
  font-weight: 500;
  letter-spacing: 0px;
  text-decoration-line: underline;
  color: #72008d;
  opacity: 1;
`;
