import React, { useState } from 'react';

import {
  Container,
  ChannelImage,
  ChannelName,
  Icon,
  ModalContainer,
} from './styles';

import { useSelector } from 'react-redux';

import DropdownIcon from '../../../../../../assets/png/seta canais@2x.png';
import ImageDefault from '../../../../../../assets/png/Android_icon_CH.png';

import ChannelList from './components/ChannelList';

function ChooseChannel() {
  const [dropDownIsOpen, toggleDropDownIsOpen] = useState(false);

  const { channelActive } = useSelector(({ channelReducer }) => channelReducer);

  return (
    <React.Fragment>
      <Container onClick={() => toggleDropDownIsOpen(true)}>
        <ChannelImage src={channelActive.itunes_image || ImageDefault} />
        <ChannelName>{channelActive.title || 'Casthood'}</ChannelName>
        <Icon src={DropdownIcon} />
      </Container>

      <ModalContainer
        onClick={() => toggleDropDownIsOpen(false)}
        isOpen={dropDownIsOpen}
      >
        <ChannelList />
      </ModalContainer>
    </React.Fragment>
  );
}

export default React.memo(ChooseChannel);
