import AddFeed from '../pages/Claim/AddFeed';
import AddByFeed from '../pages/Claim/AddByFeed';
import BuscarCanal from '../pages/Claim/BuscarCanal';
import CanalReivindicado from '../pages/Claim/CanalReivindicado';
import ReivindicarCanal from '../pages/Claim/ReivindicarCanal';
import ErroSelecionarCanal from '../pages/Claim/ErroSelecionarCanal';
import FormHelp from '../pages/Claim/FormHelp';

import ReivindicarImgs from '../assets/img/ReivindicarImgs';

const {
  addFeed,
  bemVindo,
  canalReivindicado,
  enviarEmail,
  grupoCasters,
  novaSenha,
  problemas,
} = ReivindicarImgs;

export default function FluxoReivindicacao() {
  return [
    {
      path: '/buscarcanal',
      layout: '/reivindicacao',
      Component: BuscarCanal,
      image: bemVindo,
      text: 'Procurar canal',
    },
    {
      path: '/buscarcanal/canal/:channel',
      layout: '/reivindicacao',
      Component: ReivindicarCanal,
      image: bemVindo,
      text: 'Procurar canal',
    },
    {
      path: '/add',
      layout: '/reivindicacao',
      Component: AddFeed,
      image: addFeed,
      text: 'Adicione Seu podcast',
    },
    {
      path: '/add/feed/:channel',
      layout: '/reivindicacao',
      Component: AddByFeed,
      image: addFeed,
      text: 'Adicione Seu podcast',
    },
    {
      path: '/erro/:error',
      layout: '/reivindicacao',
      Component: ErroSelecionarCanal,
      image: problemas,
      text: 'Eita! TEmos Um problema',
    },
    {
      path: '/reivindicado/:channel',
      layout: '/reivindicacao',
      Component: CanalReivindicado,
      image: canalReivindicado,
      text: 'Canal já reivindicado',
    },
    {
      path: '/help',
      layout: '/reivindicacao',
      Component: FormHelp,
      image: problemas,
      text: 'Eita! TEmos Um problema',
    },
  ];
}
