import React, { createContext, useState } from 'react';
import PropTypes from 'prop-types';

import AdItemList from './components/AdItemList';
import NewAd from './components/NewAd';

export const AdListContext = createContext({});

export default function AdScreenContext(props) {
  const { children } = props;

  /**
   * states
   */
  const [component, setComponent] = useState(<AdItemList />);

  function setNewAdComponent() {
    setComponent(<NewAd />);
  }

  function setAdItemListComponent() {
    setComponent(<AdItemList />);
  }

  return (
    <AdListContext.Provider
      value={{
        component,
        setNewAdComponent,
        setAdItemListComponent,
      }}
    >
      {children}
    </AdListContext.Provider>
  );
}

AdScreenContext.propTypes = {
  children: PropTypes.oneOfType([PropTypes.func, PropTypes.array]),
};
