import React, { createContext, useState } from 'react';

import PropTypes from 'prop-types';

import InviteItem from './components/InviteItem';

export const InviteSectionContext = createContext({});

function InviteSection(props) {
  const [inviteList, setInviteList] = useState([<InviteItem key={0} />]);

  const { children } = props;

  function appendInviteList() {
    const randomKey = Math.random().toPrecision(15).replace('.', '');
    setInviteList((prev) => [...prev, <InviteItem key={randomKey} />]);
  }

  return (
    <InviteSectionContext.Provider value={{ inviteList, appendInviteList }}>
      {children}
    </InviteSectionContext.Provider>
  );
}

export default InviteSection;

InviteSection.propTypes = {
  children: PropTypes.oneOfType([PropTypes.func, PropTypes.array]),
};
