import React from 'react';

import {
  Container,
  Table,
  TableHeaderRow,
  TableHeadColumn,
  TableBodyRow,
  TableBodyRowItem,
  CasterImage,
  CasterImageDiv,
  IconDelete,
} from './styles';

import CasterRow from './components/CasterRow';

function CastersList() {
  return (
    <Container>
      <Table>
        <TableHeaderRow>
          <TableHeadColumn>Foto</TableHeadColumn>
          <TableHeadColumn>Nome</TableHeadColumn>
          <TableHeadColumn>E-mail</TableHeadColumn>
          <TableHeadColumn>Cargo</TableHeadColumn>
        </TableHeaderRow>

        {casters.map((caster, key) => (
          <CasterRow {...{ caster }} key={key} />
        ))}
      </Table>
    </Container>
  );
}

export default React.memo(CastersList);

