export default function maskEmail(email = '###@####.com') {
  const CHAR_APPEND = '*';

  let emailParts = email.split('@');

  let emailName = emailParts[0];

  let emailDomain = emailParts[1].split('.');

  let emailShow = emailName.substr(
    Math.trunc(emailName.length / 2),
    emailName.length
  );

  let domainShow = emailDomain[0].substr(
    Math.trunc(emailDomain[0].length / 2),
    emailDomain[0].length
  );

  // mamilos@brainstorm9.com.br

  let numberAddCharToTheEmail = emailName.length - emailShow.length;
  let numberAddCharToTheDomain = emailDomain[0].length - domainShow.length;

  for (let i = 1; i <= numberAddCharToTheEmail; i++)
    emailShow = CHAR_APPEND + emailShow;

  for (let i = 1; i <= numberAddCharToTheDomain; i++)
    domainShow = CHAR_APPEND + domainShow;

  for (let i = 1; i < emailDomain.length; i++)
    domainShow = domainShow + '.' + emailDomain[i];

  return emailShow + '@' + domainShow;
}
