import React, { useContext } from 'react';

import { Container, CasterEmail, RevokeText, WithoutInvites } from './styles';

import { CastersContext } from '../../casters.context';

function InvitedList() {
  const { invitedList } = useContext(CastersContext);

  if (invitedList.length === 0) {
    return <WithoutInvites>Não há.</WithoutInvites>;
  }

  return (
    <>
      {invitedList.map((caster, key) => (
        <Container key={key}>
          <CasterEmail>{caster.email}</CasterEmail>

          <RevokeText onClick={() => revokeInvite(caster.id)}>
            Revogar
          </RevokeText>
        </Container>
      ))}
    </>
  );

  function revokeInvite(casterId) {}
}

export default InvitedList;
