import styled, { css } from 'styled-components';

const coreCheckboxStyle = css`
  min-width: 18px;
  min-height: 18px;
  border-radius: 3px;
  border: 2px solid #90268e !important;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 24px;
`;

const checkedStyle = css`
  background: #90278e;
`;

const uncheckedStyle = css`
  background: #ffffff;
`;

export const CheckboxContainer = styled.div`
  ${coreCheckboxStyle}

  ${({ checked }) => (checked ? checkedStyle : uncheckedStyle)};
`;
