import styled from 'styled-components';

export const ChannelTitle = styled.p`
  font-family: 'Poppins';
  font-size: 18px;
  font-weight: ${({ isActive }) => (isActive ? 'bold' : 'normal')};
  font-stretch: normal;
  font-style: normal;
  line-height: 1.94;
  letter-spacing: 0.36px;
  text-align: left;
  color: #72008d;
  max-width: 220px;
  white-space: nowrap;
`;

export const ChannelItem = styled.div`
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
  margin-bottom: 32px;
  margin-left: 0px;
  transition: margin-left 0.2s;

  &:last-of-type {
    margin-bottom: 48px !important;
  }

  &:hover {
    margin-left: 4px;
  }
`;

export const ChannelImage = styled.img`
  width: 47px;
  height: 47px;
  border-radius: 55px;
  margin-right: 8px;
`;
