import React, { useState, useEffect } from 'react';

import PropTypes from 'prop-types';

import { useSelector, useDispatch } from 'react-redux';

import { getChannelById } from '../../../../../../../../../../services/channel.service';

import {
  ChannelTitle,
  ChannelItem as ChannelItemSC,
  ChannelImage,
} from './styles';

function ChannelItem(props) {
  const { channelId, index } = props;

  const dispatch = useDispatch();

  const { channelActive } = useSelector(({ channelReducer }) => channelReducer);

  const [channelState, setChannelState] = useState({
    itunes_image: '',
    title: '',
  });

  useEffect(() => {
    getChannelById(channelId).then(({ data }) => {
      if (index === 0 && channelActive._id !== data._id) {
        dispatch({
          type: 'SET_CHANNEL_ACTIVE',
          payload: data,
        });
      }

      setChannelState(data);
    });
  }, [channelId]);

  return (
    <ChannelItemSC onClick={handleSetChannel}>
      <ChannelImage src={channelState.itunes_image} />
      <ChannelTitle isActive={channelState._id === channelActive._id}>
        {channelState.title}
      </ChannelTitle>
    </ChannelItemSC>
  );

  function handleSetChannel() {
    if (!channelState._id || channelActive._id === channelState._id) return;

    dispatch({
      type: 'SET_CHANNEL_ACTIVE',
      payload: channelState,
    });
  }
}

export default React.memo(ChannelItem);

ChannelItem.propTypes = {
  channelId: PropTypes.string,
  index: PropTypes.number,
};

ChannelItem.defaultProps = {
  channelId: null,
  index: 0,
};
