import React from 'react';

import PropTypes from 'prop-types';

import {
  Container,
  Content,
  Title,
  Description,
  CloseModalText,
} from './styles';

function CustomModal(props) {
  const {
    isOpen,
    children,
    toggleModalOpen,
    title,
    description,
    closeModalText,
  } = props;
  return (
    <Container {...{ isOpen }}>
      <Content>
        <Title>{title}</Title>
        <Description>{description}</Description>
        {children}

        {closeModalText.length ? (
          <CloseModalText onClick={toggleModalOpen}>
            {closeModalText}
          </CloseModalText>
        ) : (
          <></>
        )}
      </Content>
    </Container>
  );
}

export default CustomModal;

CustomModal.propTypes = {
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  isOpen: PropTypes.bool,
  children: PropTypes.oneOfType([PropTypes.func, PropTypes.array]),
  toggleModalOpen: PropTypes.func,
  closeModalText: PropTypes.string,
};

CustomModal.defaultProps = {
  isOpen: false,
  toggleModalOpen: () => {},
  closeModalText: 'Cancelar',
};
