import React, { useState, useContext, useCallback } from 'react';

import { useDropzone } from 'react-dropzone';

import {
  Container,
  Header,
  Content,
  AdForm,
  AdPreview,
  Label,
  AdFormItem,
  CloseText,
  FindFilesButton,
  FileNameContainer,
  FileNameText,
  ImageRemoveText,
} from './styles';
import { AdListContext } from '../../adList.context';

import { Title, CardDescription } from '../../../../styles';
import CustomInput from '../../../../../../../components/CustomInput';
import CustomTextArea from '../../../../../../../components/CustomTextArea';
import { CustomButton } from '../../../../../../../components/GerenciadorCanais';

import AdPreviewComponent from '../AdItemPreview';

import { useUnform } from '../../../../../../../modules/unform.module';

function NewAd() {
  const { setAdItemListComponent } = useContext(AdListContext);
  const [title, setTitle] = useState('Insira o título');
  const [description, setDescription] = useState('Descreva seu anúncio');
  const [cta, setCta] = useState('Acesse o site');
  const [website, setWebSite] = useState('Acesse o site');
  const [image, setImage] = useState(null);
  const [fileName, setFileName] = useState(null);

  const { Form, UnformInput, UnformTextArea } = useUnform();

  const onDrop = useCallback((files) => {
    // Do something with the files
    const firstFile = files[0];

    const { type } = firstFile;

    const [mimeType] = type.split('/');

    if (mimeType !== 'image') {
      return;
    }

    if (files[0]) {
      setImage(URL.createObjectURL(files[0]));
      setFileName(files[0].name);

      return;
    }
  }, []);

  const { getRootProps, getInputProps } = useDropzone({ onDrop });

  return (
    <Container>
      <Header>
        <Title>Criar um anúncio</Title>
        <CardDescription>
          Só é possível ter um anúncio ativo, se criar um novo o anterior será
          desativado
        </CardDescription>
        <br />
        <CardDescription>Todos os campos são obrigatórios</CardDescription>
      </Header>

      <Content>
        <Form onSubmit={(data) => {}}>
          <AdForm>
            <AdFormItem>
              <Label>imagem</Label>

              {fileName && (
                <FileNameContainer>
                  <ImageRemoveText onClick={handleRemoveImage}>
                    Remover
                  </ImageRemoveText>
                  <FileNameText>{fileName}</FileNameText>
                </FileNameContainer>
              )}

              <div {...getRootProps()} style={{ width: 'fit-content' }}>
                <input {...getInputProps()} />
                <FindFilesButton>Escolher arquivo</FindFilesButton>
              </div>
            </AdFormItem>
            <AdFormItem>
              <Label>Título do anúncio</Label>

              <UnformInput
                contentStyle={{ margin: 0, marginTop: -16 }}
                label=""
                name="ad_title"
                placeholder="Insira o título"
                style={{ color: '#BC79CC' }}
                onChangeCallback={(title) => setTitle(title)}
              />
            </AdFormItem>
            <AdFormItem style={{ marginBottom: 10 }}>
              <Label>Descrição*</Label>

              <UnformTextArea
                contentStyle={{ margin: 0 }}
                label=""
                name="ad_description"
                placeholder="Descreva seu anúncio"
                style={{ color: '#BC79CC' }}
                textarea
                onChangeCallback={(description) => setDescription(description)}
                maxLength={100}
              />
            </AdFormItem>
            <AdFormItem>
              <Label>CTA</Label>

              <UnformInput
                contentStyle={{ margin: 0, marginTop: -16 }}
                label=""
                name="ad_cta"
                placeholder="Acesse o site"
                style={{ color: '#BC79CC' }}
                onChangeCallback={(cta) => setCta(cta)}
              />
            </AdFormItem>
            <AdFormItem>
              <Label>Link*</Label>

              <UnformInput
                contentStyle={{ margin: 0, marginTop: -16 }}
                label=""
                name="ad_website"
                placeholder="Acesse o site"
                style={{ color: '#BC79CC' }}
                onChangeCallback={(website) => setWebSite(website)}
              />
            </AdFormItem>
            <CustomButton maxWidth>Criar</CustomButton>

            <CloseText onClick={setAdItemListComponent}>Cancelar</CloseText>
          </AdForm>
        </Form>

        <AdPreview>
          <Label>Prévia</Label>
          <AdPreviewComponent
            {...{ title, description, cta, website, image }}
          />
        </AdPreview>
      </Content>
    </Container>
  );

  function handleRemoveImage() {
    setImage(null);
    setFileName(null);
  }
}

export default NewAd;
