import styled from 'styled-components';

export const Container = styled.div`
  background: rgb(95, 0, 115);
  background: linear-gradient(180deg, #90278e 0%, #72008d 100%);
  padding: 0;
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
`;

export const Content = styled.div`
  background-color: #f0f0f0;
  width: 800px;
  height: 672px;
  border-radius: 20px;
  display: flex;
  flex-direction: row;
`;

export const ContentForm = styled.div`
  background-color: #fff;
  width: 100%;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  display: flex;
  flex-direction: column;
  padding-top: 32px;
`;

export const ContentTitle = styled.a`
  color: #72008d;
  font-family: 'Poppins';
  font-weight: 500;
  font-size: 14px;
  text-decoration: underline;
  text-transform: lowercase;
  text-align: left;

  &:hover {
    cursor: pointer;
    color: #72008d;
  }
`;

export const ContentTitleForm = styled.div`
  display: flex;
  flex-direction: column;
  text-align: left;
  width: 100%;
  margin-bottom: 48px;
`;

export const ContentDescription = styled.p`
  color: #90278e;
  font-family: 'Poppins';
  font-weight: 700;
  font-size: 35px;
  margin-bottom: 48px;
`;

export const ContentText = styled.p`
  color: #333;
  font-family: 'Poppins';
  font-weight: 200;
  font-size: 16px;
  text-align: initial;
`;

export const ButtonSingIn = styled.button`
  font-family: 'Poppins';
  display: block;
  font-weight: 700;
  color: #fff;
  font-size: 20px;
  background: #72008d;
  width: 100%;
  height: 56px;
  border-radius: 56px;

  &:hover {
    cursor: pointer;
    background-color: #90278e;
  }
`;

export const ContentFooterText = styled.p`
  color: ${(props) => (props.color ? props.color : '#72008D')};
  font-family: 'Poppins';
  font-weight: 600;
  font-size: 14px;
  text-align: ${(props) => (props.center ? 'center' : 'start')};
`;

export const ContentFooterLink = styled.a`
  color: #72008d;
  font-family: 'Poppins';
  font-weight: 600;
  font-size: 14px;
  text-align: center;
  text-decoration: underline;

  &:hover {
    cursor: pointer;
    color: #72008d;
  }
`;

export const ContentPadding = styled.div`
  padding-right: 72px;
  padding-left: 48px;
  padding-top: 120px;
`;

export const ContentTextImage = styled.p`
  color: #c365c1;
  font-family: 'Poppins';
  font-weight: 700;
  font-size: 35px;
  line-height: 53px;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 80px;
  width: 226px;
  height: 102px;
  top: 600px;
`;

export const ContentImage = styled.div`
  background-color: #f8e5ff;
  width: 100%;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 168px;
`;

export const Form = styled.form`
  width: 100%;
`;

export const FormInput = styled.div`
  margin-bottom: 32px;
  display: flex;
  flex-direction: row;
`;

export const ContentFooter = styled.div`
  display: flex;
  flex-direction: row;
  /* align-items: center; */
  justify-content: space-between;
`;

export const FeedbackSendNewLink = styled.div`
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: row;
  background: ${(props) => props.background};
  border-radius: 50px;
  width: 121px;
  height: 40px;
`;
