import styled from 'styled-components';

export const Container = styled.div`
  display: flex;
  flex-direction: column;
  max-width: 623px;
`;

export const Header = styled.div`
  margin-bottom: 32px;
`;

export const Content = styled.div`
  display: flex;
  flex-direction: row;
`;

export const AdForm = styled.div`
  margin-right: 32px;
  min-width: 290px;
`;

export const AdPreview = styled.div``;

export const AdFormItem = styled.div`
  margin-bottom: 32px;
`;

export const Label = styled.p`
  margin: 0;
  padding: 0;
  font-family: 'Poppins';
  font-size: 12px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #72008d;
  text-transform: uppercase;
`;

export const CloseText = styled.p`
  margin: 0;
  font-family: 'Poppins';
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: center;
  color: #8800a8;
  cursor: pointer;
  margin-top: 20px;
`;

export const FindFilesButton = styled.button`
  outline: hidden;
  border-radius: 24px;
  box-shadow: 0 3px 6px 0 rgba(33, 0, 41, 0.3);
  background-color: #72008d;
  padding: 8px 18px;
  font-family: Poppins;
  font-size: 12px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 0.92;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
  border: 0;
  cursor: pointer;
  margin-bottom: 8px;

  &:hover {
    background-color: #90278e;
  }
`;

export const FileNameContainer = styled.div`
  display: flex;
  flex-direction: row;
  margin-bottom: 16px;
`;

export const ImageRemoveText = styled.p`
  margin: 0;
  font-family: 'Poppins';
  font-size: 11px;
  text-decoration-line: underline;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 2;
  letter-spacing: normal;
  text-align: left;
  color: #72008d;
  margin-right: 16px;
  cursor: pointer;
`;

export const FileNameText = styled.p`
  margin: 0;
  font-family: 'Poppins';
  font-size: 11px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 2.18;
  letter-spacing: 0.22px;
  text-align: left;
  color: #000000;
`;
