import styled from 'styled-components';

export const Container = styled.div`
  border-radius: 15px;
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.4);
  background-color: #ffffff;
  padding: 25px 25px 32px;
  height: fit-content;
  max-width: 275px;
`;

export const ChannelScroll = styled.div`
  max-height: 250px;
  overflow: hidden;

  overflow-y: scroll;
  ::-webkit-scrollbar {
    width: 0px;
  }
`;

export const ClaimLink = styled.p`
  cursor: pointer;

  font-family: 'Poppins';
  font-size: 18px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.94;
  letter-spacing: 0.36px;
  text-align: left;
  color: #72008d;
  text-align: center;

  &:hover {
    color: #90278e;
    text-decoration-line: underline;
  }
`;
