import React, { useRef, useState } from 'react';

import * as Yup from 'yup';

import { Lock, Person } from '@material-ui/icons';

import { useAuth } from '../../../contexts/auth.context';

import { useUnform } from '../../../modules/unform.module';

import { Link } from 'react-router-dom';

import {
  ContentTitle,
  ContentDescription,
  ButtonSingIn,
  ContentTitleForm,
  InputContainer,
  FormContainer,
  DivForgetPassword,
  ForgotPasswordLink,
} from '../styles';

import CustomModal from '../../../components/CustomModal';

function Login({ history }) {
  const { goBack, push } = history;

  const [modalOpened, toggleModalOpened] = useState(false);

  const { singIn } = useAuth();

  const { Form, UnformInput } = useUnform();

  const formRef = useRef(null);

  async function handleSubmit(data) {
    try {
      const schema = Yup.object().shape({
        email: Yup.string().email().required('Este campo não pode ficar vazio'),
        password: Yup.string().required('Este campo não pode ficar vazio'),
      });
      await schema.validate(data, {
        abortEarly: false,
      });
      // Validation passed

      singIn(data)
        .then(() => push('/gc'))
        .catch(() => toggleModalOpened(true));
    } catch (err) {
      if (err instanceof Yup.ValidationError) {
        const validationErrors = {};
        if (err instanceof Yup.ValidationError) {
          err.inner.forEach((error) => {
            validationErrors[error.path] = error.message;
          });
          formRef.current.setErrors(validationErrors);
        }
      }
    }
  }

  return (
    <ContentTitleForm>
      <ContentTitle onClick={() => goBack()}>{'<voltar'}</ContentTitle>

      <ContentDescription>Entrar</ContentDescription>

      <Form ref={formRef} onSubmit={handleSubmit}>
        <FormContainer>
          <InputContainer>
            <UnformInput
              style={{ color: '#BC79CC' }}
              contentStyle={{ marginBottom: 32 }}
              id="email"
              name="email"
              iconStart={<Person />}
              placeholder="Email"
              label="Email"
              type="email"
            />

            <UnformInput
              style={{ color: '#BC79CC' }}
              contentStyle={{ marginBottom: 0 }}
              id="password"
              name="password"
              placeholder="Senha"
              label="Senha"
              iconStart={<Lock />}
              type="password"
            />
            <DivForgetPassword>
              <Link to="/auth/forgotpassword">
                <ForgotPasswordLink>{'Esqueceu sua senha?'}</ForgotPasswordLink>
              </Link>
            </DivForgetPassword>
          </InputContainer>
          <ButtonSingIn>Entrar</ButtonSingIn>
        </FormContainer>
      </Form>

      <CustomModal
        title="Erro!"
        description="Tivemos problemas em realizar seu login."
        toggleModalOpen={() => toggleModalOpened(false)}
        isOpen={modalOpened}
        closeModalText="Fechar"
      />
    </ContentTitleForm>
  );
}

export default React.memo(Login);
