import React from 'react';

import {
  Container,
  CoverAndTrailerSection,
  FileUploadContainer,
  AccodionContainer,
  AccodionSectionContent,
  ItemSectionChannelImage,
  ItemSectionChannelImageContainer,
  ItemSectionDescription,
  ChannelSectionContent,
} from './styles';

/**
 * components
 */
import { SectionLabel } from '../../../components/GerenciadorCanais';
import FileUpload from './components/FileUploadContainer';
import ItemSectionLabel from './components/ItemSectionLabel';
import TagsContainer from './components/TagsContainer';
import CustomAccordion from '../../../components/CustomAccordion';

import { CustomButton } from '../../../components/GerenciadorCanais';
import SocialMediasContainer from '../../../components/SocialMediasContainer';

import { useUnform } from '../../../modules/unform.module';

import { useSelector } from 'react-redux';

function MyChannel() {
  const { Form, UnformInput, UnformTextArea } = useUnform();

  const { channelActive } = useSelector(({ channelReducer }) => channelReducer);

  return (
    <Container>
      <CoverAndTrailerSection>
        <FileUploadContainer>
          <SectionLabel>Capa</SectionLabel>

          <FileUpload
            description={
              <>
                <p>1024 x 768 px</p>
                <p>jpg ou png de até 5mb</p>
              </>
            }
            acceptedFiles="image"
            onGetFileCallback={(error, file) => {}}
          />
        </FileUploadContainer>

        <FileUploadContainer>
          <SectionLabel>Áudio-trailer*</SectionLabel>
          <FileUpload
            description="Áudio em mp3"
            acceptedFiles="audio"
            onGetFileCallback={(error, file) => {}}
          />
        </FileUploadContainer>
      </CoverAndTrailerSection>

      <Form onSubmit={(data) => {}}>
        <AccodionContainer>
          <CustomAccordion borderBottom title="Itens do feed">
            <ChannelSectionContent>
              <AccodionSectionContent>
                <ItemSectionLabel
                  text="Imagem principal"
                  tooltip="Para nerds: vem da tag <image>"
                />

                <ItemSectionChannelImageContainer>
                  <ItemSectionChannelImage src={channelActive.itunes_image} />
                </ItemSectionChannelImageContainer>
              </AccodionSectionContent>
              <AccodionSectionContent>
                <ItemSectionLabel
                  text="Nome do canal"
                  tooltip="Para nerds: vem da tag <title>"
                />

                <ItemSectionDescription>
                  {channelActive.title}
                </ItemSectionDescription>
              </AccodionSectionContent>

              <AccodionSectionContent>
                <ItemSectionLabel
                  text="Descrição"
                  tooltip="Para nerds: vem da tag <description>"
                />
                <ItemSectionDescription>
                  {channelActive.description}
                </ItemSectionDescription>
              </AccodionSectionContent>
            </ChannelSectionContent>
          </CustomAccordion>

          <CustomAccordion borderBottom title="Temas">
            <TagsContainer onChangeCallBack={(tags) => console.log(tags)} />
          </CustomAccordion>

          <CustomAccordion borderBottom title="Dados">
            <ChannelSectionContent>
              <UnformTextArea
                style={{ color: '#7e0098' }}
                label="Slogan"
                placeholder="Slogan"
                name="channel-slogan"
                id="channel-slogan"
                onChangeCallback={() => {}}
                maxLength={250}
              />

              <UnformInput
                style={{ color: '#7e0098' }}
                label="Site oficial"
                placeholder="Site oficial"
                name="channel-website"
                id="channel-website"
                type="url"
                onChangeCallback={() => {}}
              />
            </ChannelSectionContent>
          </CustomAccordion>

          <CustomAccordion borderBottom title="E-mail direto">
            <ChannelSectionContent>
              <UnformInput
                style={{ color: '#7e0098' }}
                label="Email"
                placeholder="Email"
                name="channel-direct-email"
                id="channel-direct-email"
                onChangeCallback={() => {}}
              />

              <UnformTextArea
                style={{ color: '#7e0098' }}
                label="Chamada"
                placeholder="Chamada"
                name="channel-email-annotation"
                id="channel-email-annotation"
                onChangeCallback={() => {}}
                maxLength={250}
              />
            </ChannelSectionContent>
          </CustomAccordion>

          <CustomAccordion borderBottom title="Redes sociais">
            <SocialMediasContainer onChangeCallback={(value) => {}} />
          </CustomAccordion>
        </AccodionContainer>

        <CustomButton type="submit">Salvar</CustomButton>
      </Form>
    </Container>
  );
}

export default React.memo(MyChannel);
