import React, { useState } from 'react';

import SelectedChannel from './components/SelectedChannel';
import FeedbackEmail from './components/FeedbackEmail';

function ReivindicarCanal() {
  const [claimed, toggleClaimed] = useState(false);

  return (
    <>
      {claimed ? (
        <FeedbackEmail />
      ) : (
        <SelectedChannel handleClaim={toggleClaimed} />
      )}
    </>
  );
}

export default ReivindicarCanal;
