import React, { useState } from 'react';

import { RssFeed } from '@material-ui/icons';

import Wait from '../../../components/Wait';

import {
  ContentDescription,
  ContentTitleForm,
  InputContainer,
  ContentText,
  FormContainer,
  ForgotPasswordLink,
  ButtonSingIn,
} from '../styles';

import { useUnform } from '../../../modules/unform.module';

function AddFeed({ state, ...props }) {
  const [looking, toggleLooking] = useState(false);
  const { history } = props;

  const { Form, UnformInput } = useUnform();

  function sleep(ms) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }

  function handlingForm(e) {
    //TODO remover sleep
    toggleLooking(!looking); // simulando request na API
    sleep(1000).then(() => {
      // togglePodcast(channelTest);
      history.push('/reivindicacao/add/feed/:channel');
    });
  }

  return (
    <ContentTitleForm>{looking ? <Wait /> : FormSearch()}</ContentTitleForm>
  );

  function FormSearch() {
    return (
      <>
        <ForgotPasswordLink
          onClick={() => {
            history.push('/reivindicacao/buscarcanal');
          }}
        >
          {'<voltar'}
        </ForgotPasswordLink>
        <ContentDescription style={{ marginTop: 70 }}>
          Vamos começar?
        </ContentDescription>

        <ContentText>
          Insira a URL do seu Feed RSS e nosso sistema irá criar seu canal
        </ContentText>

        <Form onSubmit={(e) => handlingForm(e)}>
          <FormContainer>
            <InputContainer>
              <UnformInput
                name="channel_feed"
                id="channel_feed"
                label=""
                placeholder="Insira o link do seu Feed RSS"
                iconStart={<RssFeed />}
              />
            </InputContainer>
            <ButtonSingIn>Enviar</ButtonSingIn>
          </FormContainer>
        </Form>
      </>
    );
  }
}

export default AddFeed;

