import React from 'react';

import PropTypes from 'prop-types';

import Tooltip from '@material-ui/core/Tooltip';

import { withStyles } from '@material-ui/core/styles';

import TooltipImage from '../../../../../assets/png/Grupo 2748.png';

import { Label, Container } from './styles';

function ItemSectionLabel(props) {
  const { text, tooltip, tooltipLocation } = props;

  return (
    <Container>
      <Label>{text}</Label>
      <LightTooltip title={tooltip} placement={tooltipLocation}>
        <img
          style={{ width: 16, height: 16 }}
          src={TooltipImage}
          alt=""
          srcSet=""
        />
      </LightTooltip>
    </Container>
  );
}

ItemSectionLabel.propTypes = {
  text: PropTypes.string.isRequired,
  tooltip: PropTypes.string,
  tooltipLocation: PropTypes.oneOf([
    'bottom-end',
    'bottom',
    'bottom-start',
    'right-start',
  ]),
};

ItemSectionLabel.defaultProps = {
  text: 'Label',
  tooltip: 'Description',
  tooltipLocation: 'right-start',
};

const LightTooltip = withStyles((theme) => ({
  tooltip: {
    // backgroundColor: theme.palette.common.white,
    color: 'rgba(0, 0, 0, 0.87)',
    fontSize: 12,
    background: '#FFFFFF 0% 0% no-repeat padding-box',
    boxShadow: '0px 0px 6px #00000067',
    fontFamily: 'Poppins',
    letterSpacing: '0.24px',
    padding: 16,
  },
}))(Tooltip);

export default ItemSectionLabel;
