import React, { useContext } from 'react';

import { Container } from './styles';

/**
 * context
 */
import AdContextContainer, { AdContext } from './adScreen.context';

/**
 * components
 */
import AdTerms from './components/Terms';
import AdList from './components/AdList';

function AdScreen() {
  return (
    <AdContextContainer>
      <ChannelTermsBox />
    </AdContextContainer>
  );
}

function ChannelTermsBox() {
  const { haveReadTheTerms, invitedTheTerms } = useContext(AdContext);

  return (
    <Container>
      {haveReadTheTerms && invitedTheTerms ? <AdList /> : <AdTerms />}
    </Container>
  );
}

export default AdScreen;
