import './styles.css';

import React, { useState } from 'react';

import PropTypes from 'prop-types';

// import { Container } from './styles';

import CustomInput from '../CustomInput';

import Tooltip from '@material-ui/core/Tooltip';

import { withStyles } from '@material-ui/core/styles';

import { Option } from './components';

import TooltipImage from '../../assets/png/Grupo 2748.png';

import SetaExpandir from '../../assets/png/seta-expandir.png';

const LightTooltip = withStyles((theme) => ({
  tooltip: {
    // backgroundColor: theme.palette.common.white,
    color: 'rgba(0, 0, 0, 0.87)',
    fontSize: 12,
    background: '#FFFFFF 0% 0% no-repeat padding-box',
    boxShadow: '0px 0px 6px #00000067',
    fontFamily: 'Poppins',
    letterSpacing: '0.24px',
    padding: 16,
  },
}))(Tooltip);

/**
 * assets
 */

function CustomDropdown(props) {
  const {
    name,
    id,
    label,
    placeholder,
    tooltip,
    tooltipLocation,
    options,
    onChangeCallback,
    initialValue,
  } = props;

  const [value, setValue] = useState(initialValue);
  const [hidden, toggelHidden] = useState(false);

  return (
    <>
      <div className="dropdown">
        <div className="item-active-container">
          {/* <span>{activeText}</span> */}
          <CustomInput
            style={{ color: '#bc79cc', cursor: 'pointer' }}
            readOnly
            value={value}
            id={id}
            name={name}
            label={label}
            placeholder={placeholder}
            iconEnd={
              <img
                src={SetaExpandir}
                alt=""
                srcSet=""
                className="icon-drop-down"
                style={{
                  width: 20,
                  height: 11,
                  alignSelf: 'flex-end',
                  marginBottom: 6,
                }}
              />
            }
          />
        </div>

        <div className="dropdown-content">
          {options.map((option, key) => (
            <Option
              className="dropdown-option"
              onClick={() => {
                setValue(option);
                onChangeCallback(option);
                toggelHidden(true);
              }}
              key={key}
            >
              {option}
            </Option>
          ))}
        </div>
      </div>

      {tooltip && (
        <div
          style={{
            position: 'relative',
            top: 9,
            left: 10,
            width: 40,
            height: 40,
            margin: 0,
            padding: 0,
          }}
        >
          <LightTooltip title={tooltip} placement={tooltipLocation}>
            <img
              style={{ width: 16, height: 16 }}
              src={TooltipImage}
              alt=""
              srcSet=""
            />
          </LightTooltip>
        </div>
      )}
    </>
  );
}

export default CustomDropdown;

CustomDropdown.propTypes = {
  name: PropTypes.string.isRequired,
  id: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired,
  placeholder: PropTypes.string.isRequired,
  tooltip: PropTypes.string.isRequired,
  tooltipLocation: PropTypes.string.isRequired,
  options: PropTypes.arrayOf(PropTypes.string).isRequired,
  onChangeCallback: PropTypes.func,
  initialValue: PropTypes.string,
};

CustomDropdown.defaultProps = {
  name: '',
  id: '',
  label: '',
  placeholder: '',
  tooltip: '',
  tooltipLocation: '',
  options: [],
  onChangeCallback: () => {},
  initialValue: '',
};
