import React, { useState } from 'react';

import PropTypes from 'prop-types';

import {
  TableBodyRow,
  TableBodyRowItem,
  CasterImage,
  CasterImageDiv,
  IconDelete,
} from './styles';

import CustomModal from '../../../../../../../components/CustomModal';
import { CustomButton } from '../../../../../../../components/GerenciadorCanais';

function CasterRow(props) {
  const { caster } = props;

  const [deleteModalOpen, toggleDeleteModalOpen] = useState(false);

  return (
    <React.Fragment>
      <TableBodyRow>
        <TableBodyRowItem>
          <CasterImageDiv>
            <CasterImage src={caster.user.avatar[0]} alt="" />
          </CasterImageDiv>
        </TableBodyRowItem>
        <TableBodyRowItem>{caster.user.full_name}</TableBodyRowItem>
        <TableBodyRowItem>{caster.user.email}</TableBodyRowItem>
        <TableBodyRowItem>{caster.role}</TableBodyRowItem>
        <TableBodyRowItem>
          <IconDelete onClick={() => toggleDeleteModalOpen(!deleteModalOpen)} />
        </TableBodyRowItem>
      </TableBodyRow>

      {removeCasterModal()}
    </React.Fragment>
  );

  function removeCasterModal() {
    return (
      <CustomModal
        isOpen={deleteModalOpen}
        closeModalText="Cancelar"
        title="Remover caster"
        toggleModalOpen={() => toggleDeleteModalOpen(!deleteModalOpen)}
        description={`Você tem certeza que deseja remover ${
          caster.user.full_name
        } do canal ${'__virá do redux__'}? No futuro, se desejar incluir este caster na equipe será necessário enviar um convite novamente`}
      >
        <CustomButton onClick={() => toggleDeleteModalOpen(!deleteModalOpen)}>
          Remover caster
        </CustomButton>
      </CustomModal>
    );
  }
}

export default React.memo(CasterRow);

CasterRow.propTypes = {
  caster: PropTypes.object,
};

CasterRow.defaultProps = {
  caster: {
    user: {
      full_name: '',
      email: '',
      avatar: [],
    },
    role: '',
  },
};
