import React from 'react';

import { Link } from 'react-router-dom';

import {
  ContentTitleForm,
  ContentText,
  ForgotPasswordLink,
  FormContainer,
  InputContainer,
  ButtonSingIn,
} from '../../../styles';

import maskEmail from '../../../../../utils/maskEmail';

import {
  ChannelContainer,
  ChannelImage,
  ChannelTitle,
  ChannelEmail,
} from './styles';

function SelectedChannel({ handleClaim, history }) {
  return (
    <ContentTitleForm>
      <Link to="/reivindicacao/buscarcanal">
        <ForgotPasswordLink>{`<voltar`}</ForgotPasswordLink>
      </Link>

      <FormContainer style={{ marginTop: 90, width: 270 }}>
        <InputContainer>
          <ContentText>Você selecionou:</ContentText>

          <ChannelContainer>
            <ChannelImage src={channelTest.itunes_image} />

            <ChannelTitle>{channelTest.title}</ChannelTitle>

            <ChannelEmail>
              {maskEmail(channelTest.itunes_owner_email)}
            </ChannelEmail>
          </ChannelContainer>

          <ContentText style={{ fontSize: 14, lineHeight: '20px' }}>
            Se você reconhece o e-mail acima é só clicar em “Reivindicar” e te
            mandaremos uma confirmação nele. Se não volte, altere o seu feed com
            um e-mail válido e tente novamente.
          </ContentText>
        </InputContainer>

        <ButtonSingIn onClick={() => handleClaim(true)} maxWidth>
          Reivindicar
        </ButtonSingIn>
      </FormContainer>
    </ContentTitleForm>
  );
}

export default SelectedChannel;

