/**
 * Handlers
 */
const INITIAL_STATE = {
  selected: null,
  list: [],
};

/**
 * Actions
 */
export function fetchPodcasts() {
  return {
    type: 'ASYNC_FETCH_PODCASTS',
  };
}

export function togglePodcast(podcast) {
  return {
    type: 'ASYNC_TOGGLE_PODCAST',
    payload: podcast,
  };
}

/**
 * Reducer
 */
export default function PodcastReducers(state = INITIAL_STATE, action) {
  switch (action.type) {
    case 'FETCH_PODCASTS':
      return { ...state, list: action.payload.list };
    case 'TOGGLE_PODCAST':
      return { ...state, selected: action.payload.podcast };
    default:
      return state;
  }
}
