import styled from 'styled-components';

export const Container = styled.form`
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 16px;
  padding-left: 32px;
  padding-right: 64px;
  height: 100%;
`;

export const Header = styled.div`
  display: flex;
  justify-content: left;
  flex-direction: column;
  height: 5%;
  width: 100%;
`;

export const Form = styled.form``;

export const Content = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: baseline;
  height: 75%;
  width: 100%;
  padding-top: 72px;
`;

export const Footer = styled.div`
  display: flex;
  flex-direction: column;
  height: 20%;
  width: 100%;
`;

export const Button = styled.button`
  width: 100%;
  height: 56px;
  border-radius: 56px;
  background: #72008d 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #2100294d;
  text-align: center;
  font-family: 'Poppins';
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0;
  color: #ffffff;
  margin-top: 72px;

  &:hover {
    cursor: pointer;
    background-color: #90278e;
  }
`;

export const LinkGoBack = styled.p`
  text-decoration: underline;
  font-family: 'Poppins';
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0;
  color: #72008d;
  opacity: 1;
  &:hover {
    cursor: pointer;
    color: #90278e;
  }
`;

export const FormInput = styled.div`
  margin-bottom: 24px;
`;

export const Feedback = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  flex-direction: column;
`;

export const Badge = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 137px;
  height: 45px;
  background: #62ffb1;
  border-radius: 50px;
  margin-bottom: 36px;
`;

export const BadgeText = styled.p`
  font-family: 'Poppins';
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 0;
  color: #72008d;
  margin-left: 8px;
  text-align: center;
`;

export const Text = styled.p`
  font-family: 'Poppins';
  font-size: 16px;
  font-weight: 200;
  letter-spacing: 0;
  width: 201px;
  color: #000;
`;

export const SuccessContainer = styled.div`
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  align-items: center;
  height: 100%;
  align-self: center;
  width: 200px;
`;
