import React, { createContext, useState } from 'react';
import PropTypes from 'prop-types';

export const CastersContext = createContext({});

export default function AdScreenContext(props) {
  const { children } = props;

  /**
   * states
   */
  const [invitedList, setInvitedList] = useState([]);

  function appendInvitedList(newInvitedEmail) {
    setInvitedList((prev) => [...prev, newInvitedEmail]);
  }

  return (
    <CastersContext.Provider
      value={{
        invitedList,
        appendInvitedList,
      }}
    >
      {children}
    </CastersContext.Provider>
  );
}

AdScreenContext.propTypes = {
  children: PropTypes.oneOfType([PropTypes.func, PropTypes.array]),
};
