import React from 'react';
import { Link } from 'react-router-dom';

import ReivindicarImgs from '../../assets/img/ReivindicarImgs';

import {
  ContentTitle,
  ContentDescription,
  ContentText,
  ButtonSingIn,
  ContentFooterText,
  ContentTitleForm,
  ContentFooterLink,
  FormContainer,
  Container,
  InputContainer,
} from '../Auth/styles';

import { HomeTitle } from './styles';

import CardReivindicacao from '../../components/CardReivindicacao';

function Home({ ...props }) {
  const { grupoCasters } = ReivindicarImgs;

  return (
    <Container>
      <CardReivindicacao
        {...props}
        text="Entre pro CastHood"
        image={grupoCasters}
        Component={() => (
          <ContentTitleForm>
            <HomeTitle>Reivindicar canal</HomeTitle>
            <ContentDescription>Olá, caster!</ContentDescription>

            <FormContainer>
              <InputContainer>
                <ContentText>
                  Se você tem um podcast e quer reivindicar o seu canal no
                  CastHood é <strong>muito simples</strong>!
                  <br />
                  Só precisamos verificar se você tem acesso ao email cadastrado
                  no seu feed!
                  <br />
                  <br />
                  Para começar basta criar sua conta ou fazer o login.
                </ContentText>

                <Link to="/auth/signup">
                  <ButtonSingIn>Criar conta</ButtonSingIn>
                </Link>
              </InputContainer>
              <ContentFooterText style={{ marginTop: 24 }}>
                Já tem conta?
                <br />
                Então{' '}
                <Link to="/auth/login">
                  <ContentFooterLink>{'entre aqui>'}</ContentFooterLink>
                </Link>
              </ContentFooterText>
            </FormContainer>
          </ContentTitleForm>
        )}
      />
    </Container>
  );
}

export default Home;
