import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';

import Reivindicacao from '../layouts/Reivindicacao';
import GerenciadorCanais from '../layouts/GerenciadorCanais';
import AuthLayout from '../layouts/Auth';
import Home from '../pages/Home';

import { useAuth } from '../contexts/auth.context';

export default function Routes() {
  const { signed, sameUserToken } = useAuth();

  return (
    <Switch>
      {!signed || !sameUserToken ? (
        <>
          <Route path="/" exact render={(props) => <Home {...props} />} />
          <Route path="/auth" render={(props) => <AuthLayout {...props} />} />
          <Redirect from="" to="/" />
        </>
      ) : (
        <>
          <Route
            path="/reivindicacao"
            render={(props) => <Reivindicacao {...props} />}
          />
          <Route
            path="/gc"
            render={(props) => <GerenciadorCanais {...props} />}
          />
          <Redirect exact from="" to="/gc" />
        </>
      )}
    </Switch>
  );
}
