import React, { useState } from 'react';

import PropTypes from 'prop-types';

import {
  AdViewFeedback,
  AdImageView,
  AdFeedbackImage,
  AdFeedbackTitle,
  AdFeedbackDescription,
  AdFeedbackInformationContainer,
  AdFeedbackLinkContainer,
  AdFeedbackLink,
  AdFeedbackLinkIcon,
  AdViewFeedbackContainer,
} from './styles';

/**
 * assets
 */
import LinkIconImage from '../../../../../../../assets/png/icn-link.png';
import DefaultAdImage from '../../../../../../../assets/png/Android_icon_CH.png';

function AdItem(props) {
  const { title, description, image, cta, status } = props;

  return (
    <AdViewFeedback {...{ status }}>
      <AdImageView>
        <AdFeedbackImage src={image || DefaultAdImage} />
      </AdImageView>
      <AdFeedbackInformationContainer>
        <AdFeedbackTitle>{title}</AdFeedbackTitle>
        <AdFeedbackDescription>{description}</AdFeedbackDescription>
        <AdFeedbackLinkContainer>
          <AdFeedbackLink>{cta}</AdFeedbackLink>
          <AdFeedbackLinkIcon src={LinkIconImage} alt="link.png" />
        </AdFeedbackLinkContainer>
      </AdFeedbackInformationContainer>
    </AdViewFeedback>
  );
}

export default React.memo(AdItem);

AdItem.propTypes = {
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
  cta: PropTypes.string,
  status: PropTypes.string,
};

AdItem.defaultProps = {
  title: 'Insira o título',
  description: 'Descreva seu anúncio',
  image: DefaultAdImage,
  cta: 'Acesse o site',
  status: 'Ativado',
};
