import React, { useContext } from 'react';

import {
  Container,
  Description,
  CheckBoxContainer,
  CheckboxDefaultText,
  CheckboxStrongText,
} from './styles';

import { Title } from '../../styles';

/**
 * components
 */
import CustomCheckBox from '../../../../../components/CustomCheckBox';
import { CustomButton } from '../../../../../components/GerenciadorCanais';

/**
 * context
 */
import { AdContext } from '../../adScreen.context';

function Terms() {
  const {
    haveReadTheTerms,
    handleToggleHaveReadTheTerms,
    handleToggleInvitedTheTerms,
  } = useContext(AdContext);

  return (
    <Container>
      <Title>Sobre os anúncio(s)</Title>
      <Description>
        Aqui você pode ativar o módulo de anúncios. E as oportunidades são das
        mais variadas você pode levar seus ouvintes ao seu site, ao hotsite de
        uma promoção, a um evento que você queria anunciar, à um patrocinador
        que esteja colaborando com o seu canal!
      </Description>

      <Description>
        Só pedimos que seu(s) anúncios respeitem nossa política de comunidade.
      </Description>

      <CheckBoxContainer>
        <CustomCheckBox
          checked={haveReadTheTerms}
          onChangeCallback={handleToggleHaveReadTheTerms}
        />

        <CheckboxDefaultText>
          Declaro que li a{' '}
          <CheckboxStrongText>política de comunidade</CheckboxStrongText> e irei
          seguí-la
        </CheckboxDefaultText>
      </CheckBoxContainer>

      <CustomButton
        disabled={!haveReadTheTerms}
        onClick={handleToggleInvitedTheTerms}
      >
        Continuar
      </CustomButton>
    </Container>
  );
}

export default Terms;
