import styled, { css } from 'styled-components';

export const FieldGroup = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 1px solid;
  border-bottom-color: ${({ error }) => (error ? '#FF2727' : '#72008d')};
  border: ${({ disabled }) => disabled && 'hidden'};
  flex: 1;
`;

export const FieldContainer = styled.div`
  display: flex;
  flex-direction: row;
`;

export const IconContainer = styled.div`
  margin: 0px;
  padding: 0px;
  align-self: flex-end;
  color: ${({ error }) => (error ? '#FF2727' : '#BC79CC')};
  margin-bottom: -2px;

  svg {
    font-size: 22px !important;
  }
`;

export const Content = styled.div`
  width: 100%;
  height: 55px;
  display: flex;

  &:focus-within ${FieldGroup} {
    border-bottom: 2px solid;
    border-bottom-color: ${({ error }) => (error ? '#FF2727' : '#72008d')};
  }

  &:focus-within ${IconContainer} {
    color: ${({ error }) => (error ? '#FF2727' : '#72008d')} !important;
  }
`;

export const Field = styled.div`
  display: flex;
  flex-flow: column-reverse;
  width: 100%;
`;

export const Input = styled.input`
  font-size: 14px;
  border: 0;
  font-family: inherit;
  -webkit-appearance: none;
  border-radius: 0;
  padding: 0;
  cursor: text;
  padding-bottom: 6px;
  font-family: 'Poppins';
  color: ${({ error }) => (error ? '#FF2727' : '#BC79CC')};
  line-height: 21px;
  max-width: 100%;
  min-width: 100%;
  width: 100%;

  &:focus {
    outline: 0;
    color: ${({ error }) => (error ? '#FF2727' : '#72008d')} !important;
  }

  &:placeholder-shown + label {
    cursor: text;
    max-width: 66.66%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    transform-origin: left bottom;
    transform: translate(0, 2.125rem) scale(1.5);
  }

  &::-webkit-input-placeholder {
    opacity: 1;
    transition: inherit;
  }

  &:focus::-webkit-input-placeholder {
    opacity: 0;
  }

  &:not(:placeholder-shown) + label,
  :focus + label {
    transform: translate(0, 0) scale(1);
    cursor: pointer;
    opacity: 1;
    color: ${({ error }) => (error ? '#FF2727' : '#bc79cc')} !important;
  }

  &:focus + label {
    transform: translate(0, 0) scale(1);
    cursor: pointer;

    opacity: 1;
    color: ${({ error }) => (error ? '#FF2727' : '#bc79cc')} !important;
  }

  &:disabled {
    cursor: default;
    background-color: #ffff;
    color: #acacac !important;
  }

  &:disabled + label {
    cursor: default;
    color: #707070 !important;
  }

  &:disabled + label {
    cursor: default;
  }

  &::placeholder {
    color: ${({ error }) => (error ? '#FF2727' : '#bc79cc')};
    opacity: 0.8;
  }

  label {
    text-transform: uppercase;
    letter-spacing: 0.05em;
    color: ${({ error }) => (error ? '#FF2727' : '#bc79cc')} !important;
    font-family: 'Poppins';
    font-weight: bold;
    font-size: 12px;
    opacity: 0;
    margin-bottom: 9px;
    line-height: 18px;
    transition: all 0.2s;
    touch-action: manipulation;
  }
`;

export const TooltipContainer = styled.div`
  position: relative;
  top: 26px;
  left: 8px;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
`;

export const TootipImage = styled.img`
  width: 16px;
  height: 16px;
`;

export const FeedbackText = styled.p`
  letter-spacing: 0px;
  color: ${({ error }) => (error ? '#FF2727' : '#72008d')};
  font-size: 12px;
  font-family: 'Poppins';
  line-height: 32px;
  font-weight: normal;
`;

export const Container = styled.div`
  margin-bottom: 18px;
  width: -webkit-fill-available;
`;
