import React from 'react';
import { CardTitle, Card, ImageContainer, CardImage, Content } from './styles';

function CardReivindicacao(props) {
  const { Component, image, text } = props;

  return (
    <Card>
      <ImageContainer>
        <CardImage src={image} alt="*.png" />
        <CardTitle>{text}</CardTitle>
      </ImageContainer>

      <Content>
        <Component {...props} />
      </Content>
    </Card>
  );
}

export default CardReivindicacao;
