import React from 'react';

/**
 * Libraries
 */
import { Link } from 'react-router-dom';

/**
 * styled-components imports
 */
import {
  Container,
  Avatar,
  ProfileNameView,
  Name,
  LinksView,
  Links,
  AvatarContainer,
} from './styles';

/**
 * assets
 */
import PerfilImage from '../../../../../../assets/png/img_perfil.png';

/**
 * Context
 */
import { useAuth } from '../../../../../../contexts/auth.context';

export default function ProfileCard() {
  const { user, signOut } = useAuth();

  return (
    <Container>
      <AvatarContainer>
        <Avatar
          src={user.avatar[0] ? user.avatar[0].file_url : PerfilImage}
          alt="PerfilImage.png"
        />
      </AvatarContainer>
      <ProfileNameView>
        <Name>{`${user.first_name || user.email} ${
          user.last_name || ''
        }`}</Name>
        <LinksView>
          <Link to="/gc/minhaconta">
            <Links>Conta</Links>
          </Link>
          <div
            style={{
              marginLeft: 8,
              marginRight: 8,
              color: '#72008d',
              fontSize: 14,
              fontFamily: 'Poppins',
              fontWeight: 500,
            }}
          >
            |
          </div>
          <Links onClick={() => signOut()}>Sair</Links>
        </LinksView>
      </ProfileNameView>
    </Container>
  );
}
