/**
 * Handlers
 */
const INITIAL_STATE = {
  img: null,
  text: '',
};

/**
 * Actions
 */
export function handlingCardReivindicar(payload) {
  return {
    type: 'HANDLING_CARD',
    payload,
  };
}

/**
 * Reducer
 */
export default function CardReivindicarReducers(state = INITIAL_STATE, action) {
  switch (action.type) {
    case 'HANDLING_CARD':
      return { ...state, img: action.payload.img, text: action.payload.text };
    default:
      return state;
  }
}
