import styled, { css } from 'styled-components';

export const Container = styled.div`
  padding-left: 20px;
`;

export const CardContainer = styled.div`
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  flex-wrap: wrap;
`;

const CardCoreCss = css`
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  padding: 20px;
  width: 283px;
  height: 289px;
  margin-bottom: 20px;
  margin-right: 20px;
`;

export const FollowersCard = styled.div`
  ${CardCoreCss};

  background-color: #c479fc;
`;

export const FollowersCardImageContainer = styled.div`
  display: flex;
  width: -webkit-fill-available;
  height: -webkit-fill-available;
  align-items: center;
  justify-content: center;
`;

export const FollowersImage = styled.img`
  height: 144px;
  width: auto;
`;

export const FollowersDescriptionSection = styled.div``;

export const FollowersCardLabel = styled.p`
  margin: 0;
  font-family: 'Poppins';
  font-size: 14px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  margin-bottom: 8px;
  letter-spacing: normal;
  text-align: left;
  color: #ffffff;
`;

export const FollowersCardData = styled.p`
  margin: 0;
  font-family: 'Poppins';
  font-size: 24px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  text-align: left;
  color: #ffffff;
`;

export const MediumCard = styled.div`
  ${CardCoreCss};
  background-color: #6aa5e3;
  overflow: hidden;
`;

export const MediumCardSubTitle = styled.p`
  margin: 0;
  font-family: 'Poppins';
  font-size: 16px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  text-align: left;
  color: #ffffff;
  margin-bottom: 22px;
`;

export const MediumLinks = styled.div`
  padding-left: 20px;
  height: -webkit-fill-avalable;
  overflow-y: scroll;
  ::-webkit-scrollbar {
    width: 0px;
  }
`;

export const MediumLinksItem = styled.div`
  margin-bottom: 16px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
`;

export const HomeLink = styled.a.attrs({
  target: '_blank',
  rel: 'noopener noreferrer',
})`
  display: flex;
  flex-direction: row;
  align-items: center;
  font-family: 'Poppins';
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  text-align: left;
  color: #ffffff;
  width: 100%;
  text-decoration-line: none;
  justify-content: space-between;
`;

export const MediumLinksStyle = styled.p`
  margin: 0;
`;

export const CardHeader = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 14px;
`;

export const CardTitle = styled.p`
  margin: 0;
  text-align: left;
  font-family: 'Poppins';
  font-size: 24px;
  letter-spacing: 0px;
  color: #ffffff;
  font-weight: bold;
`;

export const MediumImg = styled.img`
  cursor: pointer;
`;
