import React, { useState, useEffect, useRef } from 'react';

/**
 * Modules
 */
import { useField } from '@unform/core';
import { Visibility, VisibilityOff } from '@material-ui/icons';
import PropTypes from 'prop-types';

import Tooltip from '@material-ui/core/Tooltip';

import { withStyles } from '@material-ui/core/styles';

import TooltipImage from '../../assets/png/Grupo 2748.png';

import {
  Container,
  Content,
  FieldGroup,
  Field,
  Input,
  IconContainer,
  TooltipContainer,
  TootipImage,
  FeedbackText,
  FieldContainer,
} from './styles';

const LightTooltip = withStyles((theme) => ({
  tooltip: {
    // backgroundColor: theme.palette.common.white,
    color: 'rgba(0, 0, 0, 0.87)',
    fontSize: 12,
    background: '#FFFFFF 0% 0% no-repeat padding-box',
    boxShadow: '0px 0px 6px #00000067',
    fontFamily: 'Poppins',
    letterSpacing: '0.24px',
    padding: 16,
  },
}))(Tooltip);

export default function CustomInput(props) {
  const {
    type,
    name,
    id,
    placeholder,
    label,
    disabled,
    value,
    iconStart,
    tooltip,
    tooltipLocation,
    readOnly,
    iconEnd,
    contentStyle,
    fieldGroupStyle,
    onChangeCallback,
    onKeyDownCallback,
    feedback,
  } = props;

  const inputRef = useRef(null);
  const { fieldName, defaultValue, registerField, error } = useField(name);

  useEffect(() => {
    registerField({
      name: fieldName,
      ref: inputRef.current,
      path: 'value',
    });
  }, [fieldName, registerField]);

  const [customType, toggleCustomType] = useState(type);

  return (
    <Container error={Boolean(error)} style={{ ...contentStyle }}>
      <FieldContainer>
        <Content error={Boolean(error)}>
          <FieldGroup
            disabled={disabled}
            error={Boolean(error)}
            style={{ ...fieldGroupStyle }}
          >
            {iconStart && (
              <IconContainer error={Boolean(error)} style={{ marginRight: 22 }}>
                {iconStart}
              </IconContainer>
            )}
            <Field error={Boolean(error)}>
              <Input
                ref={inputRef}
                onChange={({ target: { value } }) => onChangeCallback(value)}
                disabled={disabled}
                readOnly={readOnly}
                type={customType}
                name={name}
                id={id}
                placeholder={placeholder}
                error={Boolean(error)}
                value={value}
                onKeyDown={({ target: { value }, key }) => {
                  onKeyDownCallback({ value, key });
                }}
              />

              <label error={Boolean(error)} htmlFor={name}>
                {label}
              </label>
            </Field>
            {type === 'password' && (
              <IconContainer
                error={Boolean(error)}
                style={{ marginLeft: 22 }}
                onClick={() =>
                  toggleCustomType(
                    customType === 'password' ? 'text' : 'password'
                  )
                }
              >
                {customType === 'password' ? <Visibility /> : <VisibilityOff />}
              </IconContainer>
            )}

            {type !== 'password' && iconEnd && (
              <IconContainer
                error={Boolean(error)}
                style={{ marginLeft: 22 }}
                onClick={() =>
                  toggleCustomType(
                    customType === 'password' ? 'text' : 'password'
                  )
                }
              >
                {iconEnd}
              </IconContainer>
            )}
          </FieldGroup>
        </Content>
        {tooltip && (
          <TooltipContainer>
            <LightTooltip title={tooltip} placement={tooltipLocation}>
              <TootipImage src={TooltipImage} alt="" srcSet="" />
            </LightTooltip>
          </TooltipContainer>
        )}
      </FieldContainer>

      <FeedbackText error={Boolean(error)}>{error || feedback}</FeedbackText>
    </Container>
  );
}

CustomInput.propTypes = {
  type: PropTypes.oneOf(['email', 'text', 'password']),
  name: PropTypes.string,
  id: PropTypes.string,
  placeholder: PropTypes.string,
  label: PropTypes.string,
  disabled: PropTypes.bool,
  value: PropTypes.string,
  iconStart: PropTypes.node,
  iconEnd: PropTypes.node,
  textarea: PropTypes.bool,
  style: PropTypes.object,
  contentStyle: PropTypes.object,
  fieldGroupStyle: PropTypes.object,
  tooltip: PropTypes.string,
  tooltipLocation: PropTypes.oneOf(['bottom-end', 'bottom', 'bottom-start']),
  readOnly: PropTypes.bool,
  onChangeCallback: PropTypes.func.isRequired,
  onKeyDownCallback: PropTypes.func,
  feedback: PropTypes.string,
};

CustomInput.defaultProps = {
  contentStyle: {},
  fieldGroupStyle: {},
  feedback: null,
  onChangeCallback: () => {},
  onKeyDownCallback: () => {},
};
