import styled from 'styled-components';

export const Container = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
`;

export const ChannelName = styled.p`
  font-family: 'Poppins';
  font-size: 18px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: 0.36px;
  text-align: left;
  color: #d093cf;
  width: 140px;
  overflow: hidden;
  margin-right: 8px;
  white-space: nowrap;
`;

export const ChannelImage = styled.img`
  width: 47px;
  height: 47px;
  border-radius: 55px;
  margin-right: 8px;
`;

export const Icon = styled.img`
  width: 13px;
  height: 11px;
`;

export const ModalContainer = styled.div`
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  justify-content: flex-end;
  width: -webkit-fill-available;
  height: -webkit-fill-available;
  background-color: rgba(0, 0, 0, 0.4);
  display: ${({ isOpen }) => (isOpen ? 'flex' : 'none')};
  padding-right: 32px;
  padding-top: 80px;
`;
