import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';

import CardReivindicacao from '../../components/CardReivindicacao';
import FluxoReivindicacao from '../../routes/claim.routes';
import PrivateRoute from '../../components/PrivateRoute';

import { Container } from './styles';

import UserContainer from './components/User';

export default function Reivindicacao() {
  const routes = FluxoReivindicacao();

  return (
    <Container>
      <UserContainer />
      <Switch>
        {routes.map((route, key) => (
          <Route
            exact
            path={route.layout + route.path}
            render={(props) => <CardReivindicacao {...props} {...route} />}
            key={key}
          />
        ))}

        <Redirect from="/reivindicacao" to="/reivindicacao/buscarcanal" />
      </Switch>
    </Container>
  );
}
