import styled from 'styled-components';

export const Container = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  width: 210px;
  margin-bottom: 100px;
`;

export const AvatarContainer = styled.div`
  width: 52px;
  height: 52px;
  border-radius: 26px;
  overflow: hidden;
`;

export const Avatar = styled.img`
  width: inherit;
  height: auto;
`;

export const ProfileNameView = styled.div`
  display: flex;
  flex-direction: column;
  margin-left: 16px;
`;

export const Name = styled.p`
  text-align: left;
  font-family: 'Poppins';
  font-weight: bold;
  font-size: 16px;
  letter-spacing: 0px;
  letter-spacing: 0px;
  color: #707070;
  margin: 0px;
  margin-bottom: 4px;
  width: 140px;
  overflow: hidden;
`;

export const LinksView = styled.div`
  display: flex;
  flex-direction: row;
`;

export const Links = styled.p`
  margin: 0px;
  text-align: left;
  text-decoration: underline;
  font-family: 'Poppins';
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0px;
  color: #72008d;

  &:hover {
    cursor: pointer;
    color: #72008d;
  }
`;
