import React, { useRef } from 'react';

import { Person, Email, Lock } from '@material-ui/icons';

import * as Yup from 'yup';

import {
  ContentTitle,
  ContentDescription,
  ButtonSingIn,
  ContentTitleForm,
  ContentText,
  FormContainer,
  InputContainer,
} from '../styles';

import { useUnform } from '../../../modules/unform.module';
import { useAuth } from '../../../contexts/auth.context';

function AbraSuaConta({ history }) {
  const { Form, UnformInput } = useUnform();

  const { singUp } = useAuth();

  const formRef = useRef(null);

  return (
    <ContentTitleForm>
      <ContentTitle onClick={() => history.goBack()}>{'<voltar'}</ContentTitle>

      <ContentDescription style={{ marginBottom: 32 }}>
        Crie sua conta
      </ContentDescription>

      <ContentText style={{ marginBottom: 32 }}>
        Use seu <strong>e-mail pessoal</strong>! Esta ainda não é a conta do
        canal, é a sua conta de usuário.
      </ContentText>

      <Form ref={formRef} onSubmit={handlingJoin}>
        <FormContainer>
          <InputContainer>
            <UnformInput
              style={{ color: '#BC79CC' }}
              id="name"
              name="name"
              label="Nome completo"
              placeholder="Nome completo"
              type="text"
              iconStart={<Person />}
            />

            <UnformInput
              style={{ color: '#BC79CC' }}
              id="email"
              name="email"
              label="Email pessoal"
              required
              type="text"
              iconStart={<Email />}
            />

            <UnformInput
              style={{ color: '#BC79CC' }}
              id="password"
              name="password"
              label="Crie uma senha"
              type="password"
              iconStart={<Lock />}
              feedback="min. 6 caracteres contento letra(s) e número(s)"
            />
          </InputContainer>
          <ButtonSingIn>Criar</ButtonSingIn>
        </FormContainer>
      </Form>
    </ContentTitleForm>
  );

  async function handlingJoin(data) {
    try {
      const schema = Yup.object().shape({
        email: Yup.string()
          .email('Este não é um endereço de e-mail válido')
          .required('Este campo não pode ficar vazio'),
        password: Yup.string()
          .min(8, '')
          .required('Este campo não pode ficar vazio')
          .matches(
            /^(?=.*[A-Za-z])(?=.*[0-9])(?=.{8,})/,
            'min. 6 caracteres contento letra(s) e número(s)'
          ),
        name: Yup.string().required('Este campo não pode ficar vazio'),
      });
      await schema.validate(data, {
        abortEarly: false,
      });
      // Validation passed

      const { name } = data;

      const nameParts = name.split(' ');

      const last_name = nameParts[nameParts.length - 1];

      nameParts.splice(nameParts.length - 1, 1);

      let first_name = '';

      nameParts.map((text) => (first_name += `${text} `));

      const body = {
        ...data,
        first_name,
        last_name,
      };

      singUp(body)
        .then(({ data: { token } }) => {
          history.push(`/auth/signup/success/${token}`);
        })
        .catch((err) => console.error(err));
    } catch (err) {
      if (err instanceof Yup.ValidationError) {
        const validationErrors = {};
        if (err instanceof Yup.ValidationError) {
          err.inner.forEach((error) => {
            validationErrors[error.path] = error.message;
          });
          formRef.current.setErrors(validationErrors);
        }
      }
    }
  }
}

export default React.memo(AbraSuaConta);
