import styled from 'styled-components';

export const Badge = styled.div`
  display: flex;
  flex-direction: row;
  height: 45px;
  background: #ff2727;
  border-radius: 50px;
  align-items: center;
  padding-left: 24px;
  margin-bottom: 32px;
`;

export const TextBadge = styled.p`
  font-family: 'Poppins';
  font-size: 14px;
  font-weight: 800;
  letter-spacing: 0;
  color: #ffffff;
`;

export const InputLinksSection = styled.p`
  font-family: 'Poppins';
  font-size: 14px;
  text-align: center;
  font-weight: 600;
  letter-spacing: 0px;
  color: #72008d;
  opacity: 1;
  margin-top: 24px;
`;

export const InputLinksSectionUnderline = styled.p`
  font-family: 'Poppins';
  font-size: 14px;
  text-align: center;
  font-weight: 500;
  letter-spacing: 0px;
  text-decoration-line: underline;
  color: #72008d;
  opacity: 1;
  cursor: pointer;
`;

export const Footer = styled.div`
  width: -webkit-fill-available;
`;
